<template>
  <div class="ChangeNotice">
    <el-input placeholder="请输入要查询的信息"  v-model="search" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>
    <el-table
    :data="tables"
    border
    style="width: 70%;margin-left: 15%"
    stripe>
    <!-- <el-table-column
      label="日期"
      width="180">
      <template slot-scope="scope">
        <i class="el-icon-time"></i>
        <span style="margin-left: 10px" v-html="format(scope.row.date)"></span>
      </template>
    </el-table-column> -->
    <el-table-column
      fixed
      prop="noticefid"
      label="公告id"
      width="150%">
    </el-table-column>
    <el-table-column
      prop="fieldid"
      label="公告名称"
      width="200%">
    <template slot-scope="scope">
      <span v-html="format(scope.row.noticename)"></span>
  	</template>
  	</el-table-column>
    <el-table-column
      prop="fieldid"
      label="场地ID"
      width="200%">
    </el-table-column>
        <el-table-column
      prop="noticemessage"
      label="相关信息"
      width="600%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.noticemessage)"></span>
      </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      style="text-align: center"
      width="100%">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="changeRow(scope/*scope.$index*/, tableData,tables)"
          type="danger"
          size="mini">
          修改
        </el-button>
      </template>
    </el-table-column>
  </el-table>
  </el-table>

<el-dialog title="修改" lock-scroll :visible.sync="dialogFormVisible" fullscreen :close-on-press-escape=false :show-close=false>
	<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
		<el-form-item label="公告id" prop="noticefid">
			<el-input v-model="ruleForm.noticefid" disabled style="width: 75%"></el-input>
		</el-form-item>
		<el-form-item label="公告名称" prop="noticename">
			<el-input v-model="ruleForm.noticename" style="width: 75%"></el-input>
		</el-form-item>
		<el-form-item label="场地ID" prop="fieldid" status-icon>
			<el-select v-model="ruleForm.fieldid" placeholder="请选择">
				<div v-for="(item,index) in tempField">
					<el-option :label="item.fieldname" :value="item.fieldid"></el-option>
				</div>
			</el-select>
		</el-form-item>
		<el-form-item label="相关信息" prop="noticemessage">
			<el-input type="textarea" v-model="ruleForm.noticemessage" style="width: 75%"></el-input>
		</el-form-item>
		<el-form-item>
			<el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
			<el-button type="info" @click="cancelForm('ruleForm')">取消修改</el-button>
		</el-form-item>
	</el-form>
</el-dialog>
  </div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'
export default {
  data () {
    var checkLW = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('不能为空'));
        }
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error('请输入数字值'));
          } else {
              callback();
          }
        }, 1000);
      };
    return {
      search: '',
      TempValue:{
		noticefid:'',
		noticename: '',
		fieldid: '',
		noticemessage: '',
		fieldname:''
      },
      ruleForm: {
		noticefid:'',
		fieldid: '',
		noticemessage: '',
		fieldname:''
        },
      tableDataL:6,
      dialogFormVisible:false,
      tempField:[
      	{
      		fieldid:'',
      		fieldname:''
      	}
      ],
      tableData: [
        {
			noticefid:'',
			noticename: '',
			fieldid: '',
			noticemessage: '',
			fieldname:''
                                // 发送请求 赋值时候进行运算
          }
      ],
      rules: {
        	noticename:[
          	{ required: true, message: '请输入公告名称', trigger: 'blur' }
          ],
          fieldid: [
            { required: true, message: '请输入场地名称', trigger: 'blur' }
          ],
          noticemessage:[
          	{ required: true, message: '请填入详细信息', trigger: 'blur' }
          ]
        }
    }
  },
  computed: {
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }else{
        return this.tableData;
      }
    }
  },

  methods: {

     changeRow(scope, rows,searchTable) {
      var temp={
		noticename: '',
          fieldid: '',
          noticemessage: ''
      };
       this.dialogFormVisible=true;
       temp=JSON.parse(JSON.stringify(scope.row));
       this.TempValue=temp;
       this.ruleForm=scope.row;
       // 暂时存入
       

       
      },
    format (val) {
      if (val.indexOf(this.search) !== -1 && this.search !== '') {
        return val.replace(this.search, '<font color="red">' + this.search + '</font>')
      } else {
        return val;
      }
    },
    submitForm(formName) {
        this.$refs[formName].validate((valid) => {
            const loading = this.$loading({
              lock: true,
              text: 'Loading',
              spinner: 'el-icon-loading',
              background: 'rgba(0, 0, 0, 0.7)'
            });
          if (valid&&JSON.stringify(this.TempValue)!=JSON.stringify(this.ruleForm)) {
          	var control = false;

             //console.log(this.ruleForm);
        axios({
             	url:'http://localhost:8080/GMS/FieldNotice/changeNotice.do',
					method:'post',
					headers:{
		  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
						'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
  					},
					data:qs.stringify(this.ruleForm)
             }).then(resp => {
             	var tempC = resp.data;
             	if (tempC==='Success to ChangeNotice') {
						control=true;
					}
             	if (control) {
					setTimeout(() => {
						loading.close();
						this.$notify({
						title: '成功',
						message: '您新增了一个场地',
						position:'bottom-right',
						type: 'success'
					});
						this.dialogFormVisible=false;
						return true;
					},1000);
					control=false;
             	}
             }).catch(err => {
             	console.log('err');
             })
        
          }else if (valid&&JSON.stringify(this.TempValue)==JSON.stringify(this.ruleForm)) {
				setTimeout(() => {
					loading.close();
					this.$notify({
						title: '警告',
						message: '数据没有变化，请检查',
						position:'bottom-right',
						type: 'warning'
					});
						return false;
					},300);
          } else {
            console.log('error submit!!');
            
            setTimeout(() => {
              loading.close();
              this.$notify({
              title: '失败',
              message: '您的信息有误',
              position:'bottom-right',
              type: 'error'
            });
            return false;
            },300);
            
          }
        });
      },
      cancelForm(formName){
        //点击取消恢复之前的值
        this.ruleForm=this.TempValue;/*JSON.parse(JSON.stringify(this.TempValue));*/
          this.address='';
          //console.log(this.TempValue.noticemessage);
          //console.log(this.ruleForm.noticemessage);
          this.$refs.ruleForm.resetFields();
          this.dialogFormVisible=false;
        
        
      },
      getAllNotice(){
      	axios({
    		url:'http://localhost:8080/GMS/FieldNotice/getDelAll.do',
    		method:'post',
    		headers:{
    			'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',
				'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
    		}
    	}).then(resp => {
    		this.tableData = resp.data;

    	}).catch(err => {
    		console.log('err');
    	});

    	axios({
				url:'http://localhost:8080/GMS/field/getAllField.do',
				method:'post',
				headers:{
					'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
					'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
				},
			}).then(resp => {
				this.tempField = resp.data;
			}).catch(error =>{
				console.log('err');
			})
      },
  },
      created(){
      	this.getAllNotice();
      }
}

</script>

<style scoped>

</style>



