<template>
  <div class="delReg">
    <el-input placeholder="请输入要查询的信息"  v-model="search" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>
    <el-table
    :data="tables"
    border
    style="width: 90%;margin-left: 5%"
    stripe>
    <el-table-column
      fixed
      prop="regulationid"
      label="条例id"
      width="150%">
    </el-table-column>
    <el-table-column
      prop="regname"
      label="条例名称"
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.regname)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="regcontent"
      label="条例内容"
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.regcontent)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="viodispose"
      label="处理"
      width="300%">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      style="text-align: center"
      width="200%">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="deleteRow(scope/*scope.$index*/, tableData,tables)"
          type="danger"
          size="mini">
          移除
        </el-button>
      </template>
    </el-table-column>
  </el-table>

  <div style="margin-top:120px;color:#F00;font-size:30px;margin-bottom:60px">回收</div>

<el-input placeholder="请输入要查询的信息"  v-model="searcho" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>
    <el-table
    :data="tableso"
    border
    style="width: 90%;margin-left: 5%"
    stripe>
    <el-table-column
      fixed
      prop="regulationid"
      label="条例id"
      width="150%">
    </el-table-column>
    <el-table-column
      prop="regname"
      label="条例名称"
      width="300%">
      <template slot-scope="scope">
        <span v-html="formato(scope.row.regname)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="regcontent"
      label="条例内容"
      width="300%">
      <template slot-scope="scope">
        <span v-html="formato(scope.row.regcontent)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="viodispose"
      label="处理"
      width="300%">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      style="text-align: center"
      width="200%">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="recycleRow(scope/*scope.$index*/)"
          type="danger"
          size="mini">
          回收
        </el-button>
      </template>
    </el-table-column>
  </el-table>
  </div>
</template>
<script>
import axios from 'axios'
import qs from 'qs'
export default {
//注入reload
inject:['reload'],
  data () {
    return {
      search: '',
      searcho:'',
       tableData: [
        {
			regulationid:'',
			regname:'',
			regcontent:'',
			viodispose:''
          }
      ],
      tableDatao: [
        {
            regulationid:'',
			regname:'',
			regcontent:'',
			viodispose:''
          }
      ]
    }
  },

  computed: {
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }else{
        return this.tableData;
      }
    },
	tableso() {
      const searcho = this.searcho
      if (searcho) {
        return this.tableDatao.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(searcho) > -1
          })
        })
      }else{
        return this.tableDatao;
      }
    }
  },

  methods: {
  	allReg(){
  		axios({
  			url:'http://localhost:8080/GMS/Regulation/getAllReg.do',
  			method:'post',
  			headers:{
  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
				'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
  			},
  		}).then(resp => {
  			this.tableData = resp.data;

  		}).catch(error =>{
  			console.log('err');
  		});

      //获取已经删除的信息
      axios({
        url:'http://localhost:8080/GMS/Regulation/getAllRegD.do',
        method:'post',
        headers:{
          'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
        'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
        },
      }).then(resp => {
        this.tableDatao = resp.data;

      }).catch(error =>{
        console.log('err');
      })
  	},
     deleteRow(scope, rows,searchTable) {
		var control = false;
     	this.$confirm('此操作将删除该数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {

        	axios({
     		url:'http://localhost:8080/GMS/Regulation/delFieldReg.do',
     		method:'post',
     		headers:{
  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
				'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
  			},
  			data:qs.stringify({'regulationid':scope.row.regulationid}),
     	}).then(resp => {
     			var tempBool = resp.data;
     		if (tempBool = "Success to DelReg") {
     			control = true;
     		}
     		if (control) {
     			const loading = this.$loading({
		          lock: true,
		          text: 'Loading',
		          spinner: 'el-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        });
				setTimeout(() => {
					loading.close();
					this.$notify({
				      title: '成功',
				      message: '您删除了一条例',
				      position:'bottom-right',
				      type: 'success'
					});
					//刷新路由
					this.reload();
					control = false;
				},1000);
     		}else{
     			console.log('err');
     		}
     	}).catch(err => {
     		console.log('err');
     	});
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },
      recycleRow(scope){
      	//恢复
      //
      var control = false;
      axios({
        url:'http://localhost:8080/GMS/Regulation/recycleDelReg.do',
        method:'post',
        headers:{
          'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
          'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
        },
        data:qs.stringify({'regulationid':scope.row.regulationid}),
      }).then(resp => {
          var tempBool = resp.data;
        if (tempBool = "Success to RecycleReg") {
          control = true;
        }
        if (control) {
          const loading = this.$loading({
              lock: true,
              text: 'Loading',
              spinner: 'el-icon-loading',
              background: 'rgba(0, 0, 0, 0.7)'
            });
        setTimeout(() => {
          loading.close();
          this.$notify({
              title: '成功',
              message: '您恢复了一条例',
              position:'bottom-right',
              type: 'success'
          });
          //刷新路由
          this.reload();
          control = false;
        },1000);
        }else{
          console.log('err');
        }
      }).catch(err => {
        console.log('err');
      });
      },
    format (val) {
      if (val.indexOf(this.search) !== -1 && this.search !== '') {
        return val.replace(this.search, '<font color="red">' + this.search + '</font>')
      } else {
        return val;
      }
    },
  formato (val) {
      //val.indexOf(this.search) !== -1 &&
      if (val.indexOf(this.searcho)!==-1&& this.searcho !== '') {
        return val.replace(this.searcho, '<font color="red">' + this.searcho + '</font>')
      } else {
        return val;
      }
    },
  },
  created(){
  	this.allReg();
  }

}
</script>

<style scoped>

</style>


