<template>
	<div id="history">
		<el-table
    	:data="formData"
		border
		>
    <el-table-column type="expand"
    label="详情">
      <template slot-scope="props">
        <el-form 
        label-position="left" 
        inline 
        class="demo-table-expand"
        >
          <el-form-item label="预约编号：">
            <span>{{props.row.orderid}}</span>
          </el-form-item>
          <el-form-item label="预约起始时间：">
            <span>{{props.row.rentstart}}</span>
          </el-form-item>
          <el-form-item label="预约结束时间：">
            <span>{{props.row.rentend}}</span>
          </el-form-item>
          <el-form-item label="预约状态：">
            <span>{{props.row.orderstate}}</span>
          </el-form-item>
          <el-form-item label="预约场地ID：">
          	<span>{{props.row.fieldid}}</span>
          </el-form-item>
          <el-form-item label="预约场地名称：">
          	<span>{{props.row.fieldname}}</span>
          </el-form-item>
          <el-form-item label="手机号码：">
          	<span>{{props.row.phone}}</span>
          </el-form-item>
          <el-form-item label="归还编号：">
          	<span>{{props.row.returnid}}</span>
          </el-form-item>
          <el-form-item label="支付金额编号：">
          	<span>{{props.row.paymentid}}</span>
          </el-form-item>
          <el-form-item label="支付金额：">
          	<span>{{props.row.money}}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
		label="预约ID"
		prop="orderid">
	</el-table-column>
	<el-table-column
		label="预约起始时间"
		prop="rentstart"
		>		
	</el-table-column>
	<el-table-column
		label="预约结束时间"
		prop="rentend"
		>
	</el-table-column>
	<el-table-column
	label="预约状态"
	prop="orderstate">
	</el-table-column>
  </el-table>
	</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'
	import moment from 'moment'
export default{

	data(){
		return {
			userid:'',
			formData:[
			{
				orderid:'',
				rentstart:'',
				rentend:'',
				orderstate:'',
				fieldid:'',
				fieldname:'',
				phone:'',
				returnid:'',
				payid:'',
				paymentid:'',
				money:''
			}
			]
		}
	},
	methods:{
		historyByUid(){
			var data ='';
			data=this.userid;
			var HorderLength = 0;
			axios({
				url:'http://localhost:8080/GMS/FieldOrder/getHistoryByUsrid.do',
				method:'post',
				headers:{
					'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
					'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
				},
				data:qs.stringify({'userid':data})
			}).then(resp => {
				if(resp.data!=0){
					var respp = resp.data;
					this.formData = resp.data;
					for(let i=0;i<resp.data.length;i++){
								this.formData[i].rentstart = moment(parseInt(respp[i].rentstart)).format('YYYY-MM-DD hh:mm:ss');
								this.formData[i].rentend = moment(parseInt(respp[i].rentend)).format('YYYY-MM-DD hh:mm:ss');
					}
					HorderLength = resp.data.length;
					this.$emit('HorderLength',HorderLength);	
				//console.log(resp.data);
				}
			}).catch(err => {
				console.log('err');
			})
		},
  		getUidFormCookie(){
				let UsrMsg = this.$getCookie('UName');
				var uID = '';
				if (UsrMsg!='') {
					uID = UsrMsg[0];
				}
				this.userid = parseInt(uID);
				console.log(this.userid);
      		}
	},
	created(){
		this.getUidFormCookie();
		this.historyByUid();
	}

}	
</script>

<style scoped>
	.demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 100%;
  }
  /deep/ .el-button{
  	width: 40px;
  	font-size: 10px;
  	text-align: center;
  	padding-left: 10px;
  }

  /deep/ #myCancelBtn{
  	width: 70px;
  }
</style>


