<template>
	<div id="anno">
		<el-carousel 
			indicator-position="none"
			id="NoticeCar" 
			height="30px" 
			direction="vertical" 
			:interval="4000"
			:autoplay="true">
    		<el-carousel-item v-for="item in NoticeList" :key="item.index">
      			<h3 class="medium">{{ item.noticemessage }}</h3>
    		</el-carousel-item>
  		</el-carousel>
		<div id="selectAnno">
		<el-col :span="22"><el-input placeholder="请输入内容(公告名称)" v-model="noticeName" id="inputAnno">
 		</el-input></el-col>
 		<el-col :span="2"><el-button type="primary" @click="searchOne" icon="el-icon-search" id="btnAnno"></el-button></el-col>
		</div>
		<div id="NoticeTable" style="margin-top:70px">
			<el-table
			:data="annoForm"
			border
			style="width:70%;margin-left:15%"
			>
			<el-table-column
				fixd
				label="公告id"
				prop="noticefid"
				>
			</el-table-column>
			<el-table-column
				label="场地id"
				prop="fieldid">
			</el-table-column>
			<el-table-column
				label="场地名称"
				prop="fieldname">
			</el-table-column>
			<el-table-column
				label="公告名称"
				prop="noticename">
			</el-table-column>
			<el-table-column
				label="公告信息"
				prop="noticemessage">
			</el-table-column>
			<el-table-column
				label="公告状态"
				prop="noticestate">
			</el-table-column>
			</el-table>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'
	export default {
		data(){
			return{
				//numCo:'',
				noticeName:'',//转动控制  numCo==''?0:numCo
				NoticeList:[
					{
						noticename:'',
						noticemessage:''

					}
				],
				annoForm:[
					{
						noticefid:'',
						fieldid:'',
						fieldname:'',
						noticename:'',
						noticemessage:'',
						noticestate:''
					}
				]
			}
		},
		methods:{
			searchOne(){
				if (this.noticeName==0) {
					this.annoForm = [
						{
							noticefid:'',
							fieldid:'',
							fieldname:'',
							noticename:'',
							noticemessage:'',
							noticestate:''
						}
					];
				}else{
					var noticename = this.noticeName;
					axios({
						url:'http://localhost:8080/GMS/FieldNotice/getNoticeDim.do',
						method:'post',
						data:qs.stringify({'noticename':noticename}),
						headers:{
							'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
							'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
						},
					}).then(resp => {
						this.annoForm = resp.data;
					}).catch(error => {
						console.log('err');
					})
				}
			},
			getFiveNotOverTime(){
				axios({
					url:'http://localhost:8080/GMS/FieldNotice/getFiveNotice.do',
					method:'post',
					header:{
						'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
						'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
					}
				}).then(resp => {
					if(resp.data!=''){
						this.NoticeList = resp.data;
						//console.log(resp.data);
					}else{
							console.log('is Null');
							this.$message({
							type: 'warning',
							message: '系统响应公告为空！'
							});  
						}
					//计算出轮转的条数，最多五个
					//this.numCo = this.NoticeList.length-1;
				}).catch(err => {
					console.log('err');
				})
			}
		},
		created(){
			this.getFiveNotOverTime();
		}
	}
</script>

<style scoped>
	/deep/ #selectAnno{
		width: 50%;
		margin-right: 0;
		margin-left: 25%;
		margin-top: 10px;

	}
	/deep/ #inputAnno{
		border-top-left-radius: 10px;
		border-bottom-left-radius: 10px;
		border-top-right-radius: 0px;
		border-bottom-right-radius: 0px;
	}
	/deep/ #btnAnno{
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
		border-top-right-radius: 10px;
		border-bottom-right-radius: 10px;
	}
/deep/ #searchNotice{
	width: 50px;
}

/deep/ .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    margin: 0;
  }
  
  /deep/ #NoticeCar{
  	border-radius: 10px;
	width: 50%;
	margin-left: 25%;
	text-align: center;
  }

 /deep/ .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  /deep/ .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>


