<template>
	<div id="msgItemOrder">
		<el-table
    	:data="formData"
		border
		>
    <el-table-column type="expand"
    label="详情">
      <template slot-scope="props">
        <el-form 
        label-position="left" 
        inline 
        class="demo-table-expand"
        >
          <el-form-item label="预约编号：">
            <span>{{props.row.orderid}}</span>
          </el-form-item>
          <el-form-item label="预约起始时间：">
            <span>{{props.row.rentstart}}</span>
          </el-form-item>
          <el-form-item label="预约结束时间：">
            <span>{{props.row.rentend}}</span>
          </el-form-item>
          <el-form-item label="预约状态：">
            <span>{{props.row.orderstate}}</span>
          </el-form-item>
          <el-form-item label="预约场地ID：">
          	<span>{{props.row.fieldid}}</span>
          </el-form-item>
          <el-form-item label="预约场地名称：">
          	<span>{{props.row.fieldname}}</span>
          </el-form-item>
          <el-form-item label="手机号码：">
          	<span>{{props.row.phone}}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
		label="预约ID"
		prop="orderid">
	</el-table-column>
	<el-table-column
		label="预约起始时间"
		prop="rentstart"
		>		
	</el-table-column>
	<el-table-column
		label="预约结束时间"
		prop="rentend"
		>
	</el-table-column>
	<el-table-column
	label="预约状态"
	prop="orderstate">
	</el-table-column>
	<el-table-column
      fixed="right"
      label="操作"
      width="150px">
      <template slot-scope="scope">
      	<el-button 
      		:disable="IFOrder"
      		type="primary"
      		@click.native.prevent="pay(scope)"
      		size="mini">
      		支付
      	</el-button>
        <el-button
          id="myCancelBtn"
          @click.native.prevent="CancelOrder(scope, formData)"
          type="danger"
          size="mini">
          取消预约
        </el-button>
      </template>
    </el-table-column>
  </el-table>


<el-dialog title="订单确认" :visible.sync="dialogFormVisible" style="width:110%;">
  <el-form :model="ruleForm">
    <el-form-item label="需付金额" label-width="100px" style="margin-left:-30px">
      <el-input v-model="ruleForm.money" disabled></el-input>
    </el-form-item>
    <el-form-item label="时长(分钟)" label-width="100px" style="margin-left:-30px">
    	<el-input v-model="ruleForm.allTime" disabled></el-input>
    </el-form-item>
    <el-form-item label="场地ID" label-width="100px" style="margin-left:-30px">
    	<el-input v-model="ruleForm.fieldid" disabled></el-input>
    </el-form-item>
    <el-form-item label="场地名称" label-width="100px" style="margin-left:-30px">
    	<el-input v-model="ruleForm.fieldname" disabled></el-input>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false" style="width:20%">取 消</el-button>
    <el-button type="primary" @click="submitForm('ruleForm')" style="width:20%">确 定</el-button>
  </div>
</el-dialog>
	</div>
	
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'
	import moment from 'moment'
	export default {
		inject:["reload"],
		data(){
			return {
				IFOrder:true,
				dialogFormVisible :false,
				userid:'',
				formData:[
					{
						orderid:'',
						rentstart:'',
						rentend:'',
						orderstate:'',
						fieldid:'',
						fieldname:'',
						phone:'',
						allTime:'',
						unitprice:'',
						money:'',
						payid:''
					},
				],
				ruleForm:{
					money:'',
					payid:'',
					allTime:'',
					fieldid:'',
					fieldname:''
				}
			}
		},
		methods:{
			CancelOrder(scope, rows) {
				var control = false;
				const loading = this.$loading({
		          lock: true,
		          text: 'Loading',
		          spinner: 'el-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        });
		        if(scope.row.orderid!=''){
		        	var tempdata={
		        		orderid:'',
		        		fieldid:''
		        	}
		        	tempdata.orderid = scope.row.orderid;
		        	tempdata.fieldid = scope.row.fieldid;
		        	axios({
		        		url:'http://localhost:8080/GMS/FieldOrder/delOrder.do',
		        		method:'post',
						headers:{
			  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
							'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
	  					},
	  					data:qs.stringify(tempdata)
		        	}).then(resp => {

		        		var tempC = resp.data;
		        		if (tempC=='Success to CancelOrder') {
							control=true;
						}
						if(control){
							setTimeout(() => {
								loading.close();
						        this.$notify({
						          title: '注意',
						          message: '您取消了您的预约订单',
						          position:'bottom-right',
						          type: 'warning'
						        });
						        this.reload();
							},1000);	
						}else{
							setTimeout(() => {
								loading.close();
							    this.$notify({
							      title: '抱歉',
							      message: '预料之外的错误',
							      position:'bottom-right',
							      type: 'error'
							    });
							},500);
						}

		        	})

		        }else{
			        this.$notify({
			          title: '警告',
			          message: '错误操作',
			          position:'bottom-right',
			          type: 'error'
			        });
					setTimeout(() => {
						loading.close();
						// this.formData.
					},300);
		        }
      		},
      		pay(scope){
      			//此处进行订单的生成
      			//
      			this.dialogFormVisible=true;
      			this.ruleForm.money=scope.row.money;
      			this.ruleForm.allTime=scope.row.allTime;
      			this.ruleForm.fieldid=scope.row.fieldid;
      			this.ruleForm.fieldname=scope.row.fieldname;
      			this.ruleForm.payid=scope.row.payid;

      			//console.log('paying'+scope.row.payid);

      		},
      		submitForm(formName){
      			//console.log(formName)
      		//	/updateStatePay.do
      		//	
      		var control = false;

      		const loading = this.$loading({
              lock: true,
              text: 'Loading',
              spinner: 'el-icon-loading',
              background: 'rgba(0, 0, 0, 0.7)'
            });
      		if(this.ruleForm.payid!=''){
	      		axios({
	             	url:'http://localhost:8080/GMS/pay/updateStatePay.do',
						method:'post',
						headers:{
			  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
							'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
	  					},
						data:qs.stringify(this.ruleForm)
	             }).then(resp => {
	             	var tempC = resp.data;
	             	//console.log(tempC);
	             	if (tempC=='Success to UpdatePayState') {
							control=true;
						}
	             	if (control) {
						setTimeout(() => {
							loading.close();
							this.$notify({
							title: '成功',
							message: '您新增了一个场地',
							position:'bottom-right',
							type: 'success'
						});
							this.dialogFormVisible=false;
						},1000);
						control=false;
						//刷新路由
						this.reload();
	             	}
	             }).catch(err => {
	             	console.log('err');
	             })
      		}else{
      			setTimeout(() => {
      				loading.close();
					this.$notify({
					title: '失败',
					message: '这是个空数据',
					position:'bottom-right',
					type: 'error'
				});
					this.dialogFormVisible=false;
				},1000);
      		}

      		},
      		orderSelect(){
      			//传入的userid
      			var usrid='';
      			usrid = this.userid;
      			var MorderLength = 0;
      			axios({
      				url:'http://localhost:8080/GMS/FieldOrder/getListEAll.do',
      				method:'post',
      				headers:{
						'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
						'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
					},
					data:qs.stringify({'userid':usrid})
      			}).then(resp => {
					if(resp.data!=''){
						var respp = resp.data;
						this.formData = resp.data;
						//console.log(parseInt(respp[0].rentstart));
						for(let i=0;i<resp.data.length;i++){
							//计算用了多少个30分钟
							this.formData[i].allTime = (parseInt(respp[i].rentend)-parseInt(respp[i].rentstart))/1000/60;
							//计算出需付金额
							this.formData[i].money = (this.formData[i].allTime)/30*(respp[i].unitprice);


							//遍历循环转换时间戳类型为时间
							this.formData[i].rentstart = moment(parseInt(respp[i].rentstart)).format('YYYY-MM-DD hh:mm:ss');
							this.formData[i].rentend = moment(parseInt(respp[i].rentend)).format('YYYY-MM-DD hh:mm:ss');
						}
						
						//console.log(moment(parseInt(respp[0].rentstart)).format('YYYY-MM-DD hh:mm:ss'));
						//console.log(resp.data);
						;
						//向父组件传值 $emit
						MorderLength = resp.data.length;
						this.$emit('MorderLength',MorderLength);
					}else{
							this.$message({
							type: 'warning',
							message: '你的预约全部支付完毕或为空！'
							});  
						}
					//计算出轮转的条数，最多五个
					//this.numCo = this.NoticeList.length-1;
				}).catch(err => {
					console.log('err');
				})

      		},
      		getUidFormCookie(){
				let UsrMsg = this.$getCookie('UName');
				var uID = '';
				if (UsrMsg!='') {
					uID = UsrMsg[0];
					this.userid = parseInt(uID);
				}
				
      		}
		},
		created(){
			this.getUidFormCookie();
			this.orderSelect();
		}
	}
</script>

<style scoped>
	.demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 100%;
  }
  /deep/ .el-button{
  	width: 40px;
  	font-size: 10px;
  	text-align: center;
  	padding-left: 10px;
  }

  /deep/ #myCancelBtn{
  	width: 70px;
  }
</style>


