<template>
  <div class="DeleteField">
    <el-input placeholder="请输入要查询的信息"  v-model="search" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>
    <el-table
    :data="tables"
    border
    style="width: 90%;margin-left: 5%"
    stripe>
    <el-table-column
      fixed
      prop="blacklistid"
      label="黑名单id"
      width="150%">
    </el-table-column>
    <el-table-column
      prop="regulationid"
      label="条例id"
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.regulationid)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="userid"
      label="用户id"
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.userid)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="vioreason"
      label="违反条例原因"
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.vioreason)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="solution"
      label="处理"
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.solution)"></span>
      </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      style="text-align: center"
      width="200%">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="changeRow(scope/*scope.$index*/, tableData,tables)"
          type="danger"
          size="mini">
          修改
        </el-button>
      </template>
    </el-table-column>
  </el-table>


  <el-dialog lock-scroll title="修改" :visible.sync="dialogFormVisible" fullscreen :close-on-press-escape=false :show-close=false>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
		<el-form-item label="黑名单id" prop="blacklistid">
			<el-input v-model="ruleForm.blacklistid" disabled style="width: 75%"></el-input>
		</el-form-item>
		<el-form-item label="条例id" prop="regulationid">
			<el-input v-model="ruleForm.regulationid" disabled style="width: 75%"></el-input>
		</el-form-item>
		<el-form-item label="条例名称" prop="regname">
			<el-input v-model="ruleForm.regname" disabled style="width: 75%"></el-input>
		</el-form-item>
		<el-form-item label="违反原因" prop="vioreason">
			<el-input type="textarea" v-model="ruleForm.vioreason" style="width: 75%"></el-input>
		</el-form-item>
		<el-form-item label="违例处罚" prop="solution">
			<el-input type="textarea" v-model="ruleForm.solution" style="width: 75%"></el-input>
		</el-form-item>
		<el-form-item>
		<el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
		<el-button @click="cancelForm('ruleForm')">重置</el-button>
		</el-form-item>
</el-form>
  </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      search: '',
      TempValue:{
		blacklistid:'',
		regulationid:'',
		regname:'',
		userid:'',
		vioreason:'',
		solution:''
      },
      ruleForm: {
		blacklistid:'',
		regulationid:'',
		regname:'',
		userid:'',
		vioreason:'',
		solution:''
        },
      tableDataL:6,
      dialogFormVisible:false,
      tableData: [
        {
            blacklistid:'1',
            regulationid:'2',
            userid:'',
            regname:'xx',
            vioreason:'abc',
            solution:'def'
          }
      ],
      rules: {
          blacklistid: [
            { required: true, message: '不能为空', trigger: 'blur' }
          ],
          regulationid: [
            { required: true, message: ' 不能为空', trigger: 'blur' }
          ],
          userid: [
            { required: true, message: '不能为空', trigger: 'blur' }
          ],
          vioreason:[{required:true,message:'不能为空',trigger:'blur'}],
          solution:[{required:true,message:'不能为空',trigger:'blur'}],
        }
    }
  },
  computed: {
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }else{
        return this.tableData;
      }
    }
  },

  methods: {

     changeRow(scope, rows,searchTable) {
      var temp={
		blacklistid:'',
		regulationid:'',
		userid:'',
		vioreason:'',
		solution:''
      };
       this.dialogFormVisible=true;
       temp=JSON.parse(JSON.stringify(scope.row));
       this.TempValue=temp;
       this.ruleForm=scope.row;
       // 暂时存入
       

       
      },
    format (val) {
      if (val.indexOf(this.search) !== -1 && this.search !== '') {
        return val.replace(this.search, '<font color="red">' + this.search + '</font>')
      } else {
        return val;
      }
    },

    submitForm(formName) {
        this.$refs[formName].validate((valid) => {
            const loading = this.$loading({
              lock: true,
              text: 'Loading',
              spinner: 'el-icon-loading',
              background: 'rgba(0, 0, 0, 0.7)'
            });
          if (valid&&JSON.stringify(this.TempValue)!=JSON.stringify(this.ruleForm)) {
             
        setTimeout(() => {
          loading.close();
          this.$notify({
                title: '成功',
                message: '您新增了一个场地',
                position:'bottom-right',
                type: 'success'
              });
              this.dialogFormVisible=false;
          return true;
        },1000);
        
          }else if (valid&&JSON.stringify(this.TempValue)==JSON.stringify(this.ruleForm)) {
			setTimeout(() => {
				loading.close();
				this.$notify({
					title: '警告',
					message: '数据没有变化，请检查',
					position:'bottom-right',
					type: 'warning'
				});
					return false;
				},300);
          } else {
            console.log('error submit!!');
            
            setTimeout(() => {
              loading.close();
              this.$notify({
              title: '失败',
              message: '您的信息有误',
              position:'bottom-right',
              type: 'error'
            });
            return false;
            },300);
            
          }
        });
      },
      cancelForm(formName){
        //点击取消恢复之前的值
        this.ruleForm=this.TempValue;/*JSON.parse(JSON.stringify(this.TempValue));*/
          this.$refs.ruleForm.resetFields();
          this.dialogFormVisible=false;
        
        
      }
  }
}

</script>

<style scoped>

</style>



