<template>
	<div id="paidOrder">
		<el-table
    	:data="formData"
		border
		>
    <el-table-column type="expand"
    label="详情">
      <template slot-scope="props">
        <el-form 
        label-position="left" 
        inline 
        class="demo-table-expand"
        >
          <el-form-item label="预约编号：">
            <span>{{props.row.orderid}}</span>
          </el-form-item>
          <el-form-item label="预约起始时间：">
            <span>{{props.row.rentstart}}</span>
          </el-form-item>
          <el-form-item label="预约结束时间：">
            <span>{{props.row.rentend}}</span>
          </el-form-item>
          <el-form-item label="预约状态：">
            <span>{{props.row.orderstate}}</span>
          </el-form-item>
          <el-form-item label="预约场地ID：">
          	<span>{{props.row.fieldid}}</span>
          </el-form-item>
          <el-form-item label="预约场地名称：">
          	<span>{{props.row.fieldname}}</span>
          </el-form-item>
          <el-form-item label="手机号码：">
          	<span>{{props.row.phone}}</span>
          </el-form-item>
         <el-form-item label="支付编号">
         	<span>{{props.row.payid}}</span>
         </el-form-item>
         <el-form-item label="支付金额">
         	<span>{{props.row.money}}</span>
         </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
		label="预约ID"
		prop="orderid">
	</el-table-column>
	<el-table-column
		label="预约起始时间"
		prop="rentstart"
		>		
	</el-table-column>
	<el-table-column
		label="预约结束时间"
		prop="rentend"
		>
	</el-table-column>
	<el-table-column
	label="预约状态"
	prop="orderstate">
	</el-table-column>
	<el-table-column
      fixed="right"
      label="操作"
      width="150px">
      <template slot-scope="scope">
        <el-button
          id="myCancelBtn"
          @click.native.prevent="CancelOrder(scope, formData)"
          type="danger"
          size="mini">
          取消订单
        </el-button>
      </template>
    </el-table-column>
  </el-table>
	</div>
	
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'
	import moment from 'moment'
	export default {
	inject:["reload"],
		data(){
			return {
				userid:'',
				IFOrder:true,
				formData:[{
					orderid:'',
					rentstart:'',
					rentend:'',
					orderstate:'',
					fieldid:'',
					fieldname:'',
					payid:'',
					money:'',
					phone:''
				}]
			}
		},
		methods:{
			CancelOrder(scope, rows) {
				//需求，取消订单直接使得预约直接撤销
				//
				var tempdata={
					orderid:'',
	        		fieldid:''
				}
	        	tempdata.orderid = scope.row.orderid;
	        	tempdata.fieldid = scope.row.fieldid;
				var control = false;
				const loading = this.$loading({
		          lock: true,
		          text: 'Loading',
		          spinner: 'el-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        });
		        if(scope.row.orderid!=''){

		        	axios({
		        		url:'http://localhost:8080/GMS/FieldOrder/delOrder.do',
		        		method:'post',
						headers:{
			  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
							'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
	  					},
	  					data:qs.stringify(tempdata)
		        	}).then(resp => {

		        		var tempC = resp.data;
		        		if (tempC=='Success to CancelOrder') {
							control=true;
						}
						if(control){
							setTimeout(() => {
								loading.close();
						        this.$notify({
						          title: '注意',
						          message: '您取消了您的预约订单',
						          position:'bottom-right',
						          type: 'warning'
						        });
						        this.reload();
							},1000);	
						}else{
							setTimeout(() => {
								loading.close();
							    this.$notify({
							      title: '抱歉',
							      message: '预料之外的错误',
							      position:'bottom-right',
							      type: 'error'
							    });
							},500);
						}

		        	})

		        }else{
			        this.$notify({
			          title: '警告',
			          message: '错误操作',
			          position:'bottom-right',
			          type: 'error'
			        });
					setTimeout(() => {
						loading.close();
						// this.formData.
					},300);
		        }
      		},
      		getPaidOrder(){
      			var datausr = this.userid;
      			var PorderLength = 0;
      			axios({
      				url:'http://localhost:8080/GMS/FieldOrder/getPaidOrderByUsrid.do',
      				method:'post',
      				headers:{
						'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
						'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
					},
					data:qs.stringify({'userid':datausr})
      			}).then(resp => {
      				this.formData = resp.data;
					PorderLength = resp.data.length;
					this.$emit('PorderLength',PorderLength);
      			}).catch(err => {
      				console.log('paidOrder err');
      			})
      		},
      		getUidFormCookie(){
				let UsrMsg = this.$getCookie('UName');
				var uID = '';
				if (UsrMsg!='') {
					uID = UsrMsg[0];
				this.userid = parseInt(uID);
					console.log(this.userid+'用户');
				}
      		}
		},
		created(){
			this.getUidFormCookie();
			this.getPaidOrder();
		}
	}
</script>

<style scoped>
	.demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 100%;
  }
  /deep/ .el-button{
  	width: 40px;
  	font-size: 10px;
  	text-align: center;
  	padding-left: 10px;
  }

  /deep/ #myCancelBtn{
  	width: 70px;
  }
</style>


