<template>
	<div id="addReg"  style="width:90%;margin-left: 10%">
		<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
		  	<el-form-item label="条例名称" prop="regname">
		    	<el-input v-model="ruleForm.regname" style="width: 75%"></el-input>
		 	 </el-form-item>
		  	<el-form-item label="条例内容" prop="regcontent">
		    	<el-input type="text" v-model="ruleForm.regcontent" style="width: 75%"></el-input>
		  	</el-form-item>
		  	<el-form-item label="违例处理" prop="viodispose">
		    	<el-input type="textarea" v-model="ruleForm.viodispose" style="width: 75%"></el-input>
		  	</el-form-item>
		  </el-form-item>
		  <el-form-item>
		    <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
		    <el-button @click="resetForm('ruleForm')">重置</el-button>
		  </el-form-item>
		</el-form>
	</div>
</template>

<script>
		import axios from 'axios'
	import qs from 'qs'
	export default{
		data(){
			return {
				rules:{
					regname:[
						{required:true,message:'不能为空，请输入',trigger:'blur'}
					],
					regcontent:[
						{required:true,message:'不能为空，请输入',trigger:'blur'}
					],
		            viodispose: [
		            	{ required: true, message: '不能为空', trigger: 'blur' }
		          	]
				},
				ruleForm:{
						regname:'',
						regcontent:'',
						viodispose:''
				}
			}
		},
		methods:{
			submitForm(formName){
				//console.log(this.ruleForm);
				this.$refs[formName].validate((valid) => {
        		const loading = this.$loading({
		          lock: true,
		          text: 'Loading',
		          spinner: 'el-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        });
					if(valid){
				var control = false;
				axios({
					url:'http://localhost:8080/GMS/Regulation/addRegulation.do',
					method:'post',
					headers:{
		  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
						'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
  					},
					data:qs.stringify(this.ruleForm)
				}).then(resp => {
					var tempC = resp.data;
					//console.log(tempC);
					if (tempC==='Success to AddReg') {
						control=true;
					}
					if (control) {
						setTimeout(() => {
							loading.close();
							this.$notify({
					          title: '成功',
					          message: '您新增了一个条例',
					          position:'bottom-right',
					          type: 'success'
				        	});
							return true;
						},1000);
						//置为默认值
						this.ruleForm={
							regname:'',
							regcontent:'',
							viodispose:''
						}
        					//重置表单判断
        					this.$refs[formName].resetFields();
						control=false;
					}else{
						console.log('err')
					}
				})
				
				
          } else {
            console.log('error submit!!');
            
            setTimeout(() => {
            	loading.close();
            	this.$notify({
		          title: '失败',
		          message: '您的信息有误',
		          position:'bottom-right',
		          type: 'error'
		        });
		        return false;
            },300);
            
          }
				})
			},
			resetForm(formName){
				this.$refs[formName].resetFields();
			}
		}
	}
</script>

<style scoped>
	
</style>


