<template>
  <div class="changeReg">
    <el-input placeholder="请输入要查询的信息"  v-model="search" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>
    <el-table
    :data="tables"
    border
    style="width: 90%;margin-left: 5%"
    stripe>
    <el-table-column
      fixed
      prop="regulationid"
      label="条例id"
      width="150%">
    </el-table-column>
    <el-table-column
      prop="regname"
      label="条例名称"
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.regname)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="regcontent"
      label="条例内容"
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.regcontent)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="viodispose"
      label="处理"
      width="300%">
	<template slot-scope="scope">
        <span v-html="format(scope.row.viodispose)"></span>
	</template>
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      style="text-align: center"
      width="100%">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="changeRow(scope/*scope.$index*/, tableData,tables)"
          type="danger"
          size="mini">
          修改
        </el-button>
      </template>
    </el-table-column>
  </el-table>

  <el-dialog lock-scroll title="修改" :visible.sync="dialogFormVisible" fullscreen :close-on-press-escape=false :show-close=false>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
		<el-form-item label="条例id" prop="blacklistid">
			<el-input v-model="ruleForm.regulationid" disabled style="width: 75%"></el-input>
		</el-form-item>
		<el-form-item label="条例名称" prop="regname">
			<el-input v-model="ruleForm.regname" style="width: 75%"></el-input>
		</el-form-item>
		<el-form-item label="条例内容" prop="regcontent">
			<el-input v-model="ruleForm.regcontent" style="width: 75%"></el-input>
		</el-form-item>
		<el-form-item label="处理" prop="viodispose">
			<el-input type="textarea" v-model="ruleForm.viodispose" style="width: 75%"></el-input>
		</el-form-item>
		<el-form-item>
		<el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
		<el-button @click="cancelForm('ruleForm')">重置</el-button>
		</el-form-item>
</el-form>
  </el-dialog>
  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
export default {
  data () {
    return {
      search: '',
      TempValue:{
		regulationid:'',
		regname:'',
		regcontent:'',
		viodispose:''
      },
      ruleForm: {
		regulationid:'',
		regname:'',
		regcontent:'',
		viodispose:''
        },
      tableDataL:6,
      dialogFormVisible:false,
      tableData: [
        {
			regulationid:'',
			regname:'',
			regcontent:'',
			viodispose:''
          }
      ],
      rules: {
          regulationid: [
            { required: true, message: '不能为空', trigger: 'blur' }
          ],
          regname: [
            { required: true, message: ' 不能为空', trigger: 'blur' }
          ],
          regcontent: [
            { required: true, message: '不能为空', trigger: 'blur' }
          ],
          viodispose: [
            { required: true, message: '不能为空', trigger: 'blur' }
          ],
          viodispose: [
            { required: true, message: '不能为空', trigger: 'blur' }
          ]
        }
    }
  },
  computed: {
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }else{
        return this.tableData;
      }
    }
  },

  methods: {

     changeRow(scope, rows,searchTable) {
      var temp={
		blacklistid:'',
		regulationid:'',
		userid:'',
		vioreason:'',
		solution:''
      };
       this.dialogFormVisible=true;
       temp=JSON.parse(JSON.stringify(scope.row));
       this.TempValue=temp;
       this.ruleForm=scope.row;
       // 暂时存入
       

       
      },
    format (val) {
      if (val.indexOf(this.search) !== -1 && this.search !== '') {
        return val.replace(this.search, '<font color="red">' + this.search + '</font>')
      } else {
        return val;
      }
    },
    submitForm(formName) {
    	//console.log(this.ruleForm);
        this.$refs[formName].validate((valid) => {
            const loading = this.$loading({
              lock: true,
              text: 'Loading',
              spinner: 'el-icon-loading',
              background: 'rgba(0, 0, 0, 0.7)'
            });
          if (valid&&JSON.stringify(this.TempValue)!=JSON.stringify(this.ruleForm)) {
          	var control = false;
             axios({
             	url:'http://localhost:8080/GMS/Regulation/changeFieldReg.do',
					method:'post',
					headers:{
		  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
						'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
  					},
					data:qs.stringify(this.ruleForm)
             }).then(resp => {
             	var tempC = resp.data;
             	//console.log(tempC);
             	if (tempC=='Success to ChangeReg') {
						control=true;
					}
             	if (control) {
					setTimeout(() => {
						loading.close();
						this.$notify({
						title: '成功',
						message: '您修改了一个条例',
						position:'bottom-right',
						type: 'success'
					});
						this.dialogFormVisible=false;
						return true;
					},1000);
					control=false;
             	}
             }).catch(err => {
             	console.log('err1');
             })
        
          }else if (valid&&JSON.stringify(this.TempValue)==JSON.stringify(this.ruleForm)) {
			setTimeout(() => {
				loading.close();
				this.$notify({
					title: '警告',
					message: '数据没有变化，请检查',
					position:'bottom-right',
					type: 'warning'
				});
					return false;
				},300);
          } else {
            console.log('error submit!!');
            
            setTimeout(() => {
              loading.close();
              this.$notify({
              title: '失败',
              message: '您的信息有误',
              position:'bottom-right',
              type: 'error'
            });
            return false;
            },300);
            
          }
        });
      },
      allReg(){
  		axios({
  			url:'http://localhost:8080/GMS/Regulation/getAllReg.do',
  			method:'post',
  			headers:{
  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
				'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
  			},
  		}).then(resp => {
  			this.tableData = resp.data;

  		}).catch(error =>{
  			console.log('err');
  		});
      },
      cancelForm(formName){
        //点击取消恢复之前的值
        this.ruleForm=this.TempValue;/*JSON.parse(JSON.stringify(this.TempValue));*/
          this.$refs.ruleForm.resetFields();
          this.dialogFormVisible=false;
        
        
      }
  },
  created(){
  	this.allReg();
  }
}

</script>

<style scoped>

</style>



