<template>
	<div class="addField" style="width: 90%;margin-left: 10%">
	<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
  <el-form-item label="公告名称" prop="noticename">
    <el-input v-model="ruleForm.noticename" style="width: 75%"></el-input>
  </el-form-item>
  <el-form-item label="场地ID" prop="fieldid">
  	<el-select v-model="ruleForm.fieldid" placeholder="请选择">
    <el-option
      v-for="item in options"
      :key="item.fieldid"
      :label="item.fieldname"
      :value="item.fieldid"
      >
    </el-option>
  </el-select>
  	<!--<el-input type="text" v-model="ruleForm.fieldid" style="width: 75%" @blur="importMsg"  @focus="setNull"></el-input>-->
  </el-form-item>
  <el-form-item label="相关信息" prop="noticemessage">
    <el-input type="textarea" v-model="ruleForm.noticemessage" style="width: 75%"></el-input>
  </el-form-item>
	<!--<el-form-item label="场地名称">
		<el-input
		  style="width:75%"
		  placeholder="导入信息：名称"
		  v-model="fieldname"
      v-loading="loading"
		  :disabled="true"></el-input>
	</el-form-item>-->
  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm')">添加</el-button>
    <el-button type="info" @click="resetForm('ruleForm')">重置</el-button>
  </el-form-item>
</el-form>
	</div>
</template>
<script>
	import axios from 'axios'
	import qs from 'qs'
  export default {
    data() {

      return {
        loading:false,
      	//fieldname:'',
		ruleForm: {
			noticename: '',
			fieldid: '',
			noticemessage: ''
        },
        rules: {
        	noticename:[
          	{ required: true, message: '请输入公告名称', trigger: 'blur' }
          ],
          noticemessage:[
          	{ required: true, message: '请输入公告信息', trigger: 'blur' }
          ],
          fieldid: [
            { required: true, message: '请选择场地ID', trigger: 'blur' },
          ]
        },options: [
	        {
	          fieldid:'',
	          fieldname:''
	        }
        ],
      }
    },
    methods: {
    	importMsg(){
    		//console.log("getFieldMsg");
        //if (this.ruleForm.fieldid!=='') {
        //this.loading=false;
        var control=false;
    		//this.fieldname='东区';
    		var data = this.ruleForm.fieldid;
    		axios({
				url:'http://localhost:8080/GMS/field/getAllField.do',
				method:'post',
				//data:qs.stringify({'fieldid':data}),
				headers:{
					'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
					'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
				},
					}).then(resp => {
						if (resp.data!=0) {
						var tempData = resp.data;
						this.options = tempData;
						}

			}).catch(error =>{
				console.log('err');
			})
        //}else{
          //this.loading=true;
          //this.fieldname='';
        //}
    	},
    	//setNull(){
    		//this.fieldname=''
    	//},
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
        		const loading = this.$loading({
		          lock: true,
		          text: 'Loading',
		          spinner: 'el-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        });
          if (valid) {
         	var control = false;
         	axios({
         		url:'http://localhost:8080/GMS/FieldNotice/addNotice.do',
         		method:'post',
         		headers:{'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
					'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
				},
         		data:qs.stringify(this.ruleForm)
         	}).then(resp => {
         		var tempC = resp.data;
         		if (tempC=='Success to AddNotice') {
         			control = true;
         		}
         		if (control) {
         			setTimeout(() => {
					this.$notify({
			          title: '成功',
			          message: '您新增了一个公告',
			          position:'bottom-right',
			          type: 'success'
		        	});
					return true;
				},1000);
         			loading.close();
         			//重置表单数据判断
         			this.$ref[formName].resetFields();
         		}else{
         			console.log('err');
         		}
         	}).catch(err => {
         		console.log('request err')
         	})
				
				
          } else {
            console.log('error submit!!');
            
            setTimeout(() => {
            	loading.close();
            	this.$notify({
		          title: '失败',
		          message: '您的信息有误',
		          position:'bottom-right',
		          type: 'error'
		        });
		        return false;
            },300);
            
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.loading=false;
        this.fieldname='';
      }
    },
    created(){
    	this.importMsg();
    }
  }
</script>

<style>
	
</style>


