<template>
  <div class="DeleteField">
    <el-input placeholder="请输入要查询的信息"  v-model="search" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>
    <el-table
    :data="tables"
    border
    style="width: 90%;margin-left: 5%"
    stripe>
    <!-- <el-table-column
      label="日期"
      width="180">
      <template slot-scope="scope">
        <i class="el-icon-time"></i>
        <span style="margin-left: 10px" v-html="format(scope.row.date)"></span>
      </template>
    </el-table-column> -->
    <el-table-column
    prop="fieldid"
    label="场地ID"
    width="150%"
    fixed>
    </el-table-column>
    <el-table-column
      prop="fieldname"
      label="场地名称"
      width="150%">
    <template slot-scope="scope">
      <span v-html="format(scope.row.fieldname)"></span>
    </template>
    </el-table-column>
    <el-table-column
      prop="fieldtype"
      label="类型"
      width="200%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.fieldtype)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="fieldstate"
      label="场地状态"
      width="200%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.fieldstate)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="fieldwidth"
      label="场地宽度"
      width="200%">
    </el-table-column>
    <el-table-column
      prop="fieldlong"
      label="场地长度"
      width="200%">
    </el-table-column>
    <el-table-column
      prop="unitprice"
      label="场地单价(元)"
      width="200%">
    </el-table-column>
    <el-table-column
      prop="address"
      label="场地位置"
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.address)"></span>
      </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      style="text-align: center"
      width="200%">
      <template slot-scope="scope">
        <el-button @click="handleClick(scope.row)" type="primary" style="margin-left: 15%" size="mini">查看</el-button>
        <el-button
          @click.native.prevent="deleteRow(scope/*scope.$index*/, tableData,tables)"
          type="danger"
          size="mini">
          移除
        </el-button>
      </template>
    </el-table-column>
  </el-table>

<div style="margin-top:120px;color:#F00;font-size:30px;margin-bottom:60px">恢复场地</div>

<el-input placeholder="请输入要查询的信息"  v-model="searcho" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>
    <el-table
    :data="tableso"
    border
    style="width: 90%;margin-left: 5%"
    stripe>
    <el-table-column
    prop="fieldid"
    label="场地ID"
    width="150%"
    fixed>
    </el-table-column>
    <el-table-column
      prop="fieldname"
      label="场地名称"
      width="150%">
    <template slot-scope="scope">
      <span v-html="formato(scope.row.fieldname)"></span>
    </template>
    </el-table-column>
    <el-table-column
      prop="fieldtype"
      label="类型"
      width="200%">
      <template slot-scope="scope">
        <span v-html="formato(scope.row.fieldtype)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="fieldstate"
      label="场地状态"
      width="200%">
      <template slot-scope="scope">
        <span v-html="formato(scope.row.fieldstate)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="fieldwidth"
      label="场地宽度"
      width="200%">
    </el-table-column>
    <el-table-column
      prop="fieldlong"
      label="场地长度"
      width="200%">
    </el-table-column>
    <el-table-column
      prop="unitprice"
      label="场地单价(元)"
      width="200%">
    </el-table-column>
    <el-table-column
      prop="address"
      label="场地位置"
      width="300%">
      <template slot-scope="scope">
        <span v-html="formato(scope.row.address)"></span>
      </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      style="text-align: center"
      width="200%">
      <template slot-scope="scope">
        <el-button @click="handleClick(scope.row)" type="primary" style="margin-left: 15%" size="mini">查看</el-button>
        <el-button
          @click.native.prevent="recycleRow(scope/*scope.$index*/)"
          type="danger"
          size="mini">
          回收
        </el-button>
      </template>
    </el-table-column>
  </el-table>



  		<el-dialog lock-scroll title="查看" :visible.sync=dialogFormVisible :close-on-press-escape=false :show-close=false>
      <div style="height:550px">
        <el-card shadow="hover" style="height:350px;margin-top:-30px"body-style="{ padding: '0px' }">
        <el-image :src="tableSite.fieldimg" fit="fill" lazy style="width: 100%;height: 20%">
            <div slot="error" class="image-slot">
                <i class="el-icon-picture-outline"></i>
              </div>
          </el-image>
        </el-card>
          <div style="margin-top:20px;">
          <el-card shadow="hover" style="padding-left:20%;margin-top: 3px;height:200px"body-style="{ padding: '0px' }">
          <span style="font-size:16px">场地名字:{{tableSite.fieldname}}</span><br>
          <span style="font-size:16px">场地类型：{{tableSite.fieldtype}}</span><br>
          <span style="font-size:16px">场地状态：{{tableSite.fieldstate}}</span><br>
          <span style="font-size:16px">场地长度：&nbsp;&nbsp;米</span><br>
          <span style="font-size:16px">场地宽度：{{tableSite.fieldwidth}}&nbsp;&nbsp;米</span><br>
          <span style="font-size:16px">场地位置：{{tableSite.address}}</span><br>
          <span style="font-size:16px">场地租赁价格(每小时)：￥{{tableSite.unitprice}}</span><br>
          </el-card>  
        </div>
    </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
export default {

	//注入reload
	inject:['reload'],
  data () {
    return {
      /*{
            tIndex:0,
            FieldName:'东区体育馆场地1',
            type:'篮球场',
            FieldState:'维护中',
            FieldWidth:10,
            FieldLong:20,
                                // 发送请求 赋值时候进行运算
            address:'xxxxxxxxxxxxxxxxxxxx'
          }*/
          dialogFormVisible:false,
      tableSite:{
  				fieldimg:'',
  				fieldtype:'',
  				fieldinout:'',
  				fieldstate:'',
  				fieldlong:'',
  				fieldwidth:'',
  				fieldname:'',
  				address:'',
  				fieldimg:'',
  				unitprice:''
			},
      search: '',
      searcho:'',
      tableData: [
        
        {
        	fieldid:'',
          fieldimg:'',	
    			fieldtype:'',
    			fieldinout:'',
    			fieldstate:'',
    			fieldlong:'',
    			fieldwidth:'',
    			fieldname:'',
    			address:'',
    			unitprice:''
		}
      ],
      tableDatao: [
        
        {
          fieldid:'',
          fieldimg:'',  
          fieldtype:'',
          fieldinout:'',
          fieldstate:'',
          fieldlong:'',
          fieldwidth:'',
          fieldname:'',
          address:'',
          unitprice:''
    }
      ]
    }
  },

  computed: {
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }else{
        return this.tableData;
      }
    },
    tableso() {
      const searcho = this.searcho
      if (searcho) {
        return this.tableDatao.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(searcho) > -1
          })
        })
      }else{
        return this.tableDatao;
      }
    }
  },

  methods: {
  	allSite(){
  		axios({
  			url:'http://localhost:8080/GMS/field/getAllField.do',
  			method:'post',
  			headers:{
  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
				'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
  			},
  		}).then(resp => {
  			this.tableData = resp.data;

  		}).catch(error =>{
  			console.log('err');
  		});

      //获取已经删除的信息
      axios({
        url:'http://localhost:8080/GMS/field/getAllwDel.do',
        method:'post',
        headers:{
          'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
        'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
        },
      }).then(resp => {
        this.tableDatao = resp.data;

      }).catch(error =>{
        console.log('err');
      })
  	},
    handleClick (row) {
    	if (row.fieldid!='') {

    	this.dialogFormVisible=true;
    	this.tableSite=row;
		//console.log(row);
    	}else{
     		this.$message.error('错了哦，接受不了后台数据啦！请重新刷新');
    	}
    },
     deleteRow(scope, rows,searchTable) {
     	if (scope.row.fieldid!='') {

     	//
     	var control = false;
     	this.$confirm('此操作将删除该数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {

        	axios({
     		url:'http://localhost:8080/GMS/field/delField.do',
     		method:'post',
     		headers:{
  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
				'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
  			},
  			data:qs.stringify({'fieldid':scope.row.fieldid}),
     	}).then(resp => {
     			var tempBool = resp.data;
     		if (tempBool = "Success to DeleteField") {
     			control = true;
     		}
     		if (control) {
     			const loading = this.$loading({
		          lock: true,
		          text: 'Loading',
		          spinner: 'el-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        });
				setTimeout(() => {
					loading.close();
					this.$notify({
				      title: '成功',
				      message: '您删除了一个场地',
				      position:'bottom-right',
				      type: 'success'
					});
					//刷新路由
					this.reload();
					control = false;
				},1000);
     		}else{
     			console.log('err');
     		}
     	}).catch(err => {
     		console.log('err');
     	});
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
       
     	}else{
     		this.$message.error('错了哦，接受不了后台数据啦！请重新刷新');
     	}
      },
    format (val) {
    	//val.indexOf(this.search) !== -1 &&
      if (val.indexOf(this.search)!==-1&& this.search !== '') {
        return val.replace(this.search, '<font color="red">' + this.search + '</font>')
      } else {
        return val;
      }
    },
    formato (val) {
      //val.indexOf(this.search) !== -1 &&
      if (val.indexOf(this.searcho)!==-1&& this.searcho !== '') {
        return val.replace(this.searcho, '<font color="red">' + this.searcho + '</font>')
      } else {
        return val;
      }
    },
    recycleRow(scope){
    	if (scope.row.fieldid!='') {

      //console.log(scope);
      //recycleFieldFromDel
      //恢复
      //
      var control = false;
      axios({
        url:'http://localhost:8080/GMS/field/recycleFieldFromDel.do',
        method:'post',
        headers:{
          'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
          'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
        },
        data:qs.stringify({'fieldid':scope.row.fieldid}),
      }).then(resp => {
          var tempBool = resp.data;
        if (tempBool = "Success to RecycleField") {
          control = true;
        }
        if (control) {
          const loading = this.$loading({
              lock: true,
              text: 'Loading',
              spinner: 'el-icon-loading',
              background: 'rgba(0, 0, 0, 0.7)'
            });
        setTimeout(() => {
          loading.close();
          this.$notify({
              title: '成功',
              message: '您恢复了一个场地',
              position:'bottom-right',
              type: 'success'
          });
          //刷新路由
          this.reload();
          control = false;
        },1000);
        }else{
          console.log('err');
        }
      }).catch(err => {
        console.log('err');
      });
    	}else{
     		this.$message.error('错了哦，接受不了后台数据啦！请重新刷新');
     	}
    }
  },
  created(){
  	this.allSite();
  }

}
</script>

<style scoped>

</style>


