<template>
	<transition enter-active-class="animated slideInRight" leave-active-class="animated slideOutRight">
		<div class="loginform" v-show="false">
			<div id="lform">
				<el-button id="closeForm" type="danger" icon="el-icon-close" @click="close" circle></el-button>
				<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="20%" class="demo-ruleForm">
					<el-form-item label="账号" prop="username">
					<el-input v-model.number="ruleForm.username" style="width: 75%"></el-input>
					</el-form-item>
					<el-form-item label="密码" prop="pass">
					<el-input type="password" v-model="ruleForm.pass" autocomplete="off" style="width: 75%"></el-input>
					</el-form-item>
					<el-form-item>
					<el-button type="primary" @click="submitForm('ruleForm')" style="margin-left: 15%">提交</el-button>
					<el-button @click="resetForm('ruleForm')" style="margin-left: 5%">重置</el-button>
					</el-form-item>
				</el-form>
			</div>
		</div>	
	</transition>
</template>

<script>
	import { mapGetters,mapActions } from "vuex";
	//动画引入
	import 'animate.css/animate.css'


	export default {
		data(){
			var checkUsername = (rule, value, callback) => {
        if (!value) {
          callback(new Error('账号不能为空'));
        }
        setTimeout(() => {
        	if(!/^20[0-9]{10}/.test(value)){
        	callback(new Error('格式错误，应为"20xxxxxxxxxx",x全为数字!'));
        	}else{
        		callback();
        	}
        },1000);
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      return {
        ruleForm: {
          pass: '',
          username: ''
        },
        rules: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          username: [
            { validator: checkUsername, trigger: 'blur' }
          ]
        }
      };
		},
		methods:{
			close(){
				this.$store.dispatch('LoginHS');
			},
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
				if (valid) {
					this.$store.dispatch('LoginHS');
					this.$message({
			          message: '恭喜你，登录成功',
			          type: 'success'
			        });
				} else {
					console.log('error submit!!');
					this.$message.error('登录失败，输入内容错误！');
					return false;
				}
				});
			},
			resetForm(formName) {
        		this.$refs[formName].resetFields();
      		}	
		},
		computed:{
			...mapGetters([
				//'LoginFH'
			])
		},

	}
</script>

<style scoped>
	.loginform{
		position: fixed;
		top: 0;
		left: 0;
		width:100%;
		height: 100%;
		z-index: 666;
		background-color: rgba(150,150,150,0.7);
	}
	#lform{
		position:fixed;
		top: 40%;
		left: 20%;
		padding-top: 50px;
		padding-left: 20px;
		margin-left: 9.2%;
		width: 40%;
		overflow: auto;
		background-color: rgba(255, 255, 255, 0.8);
		color: #fff;
		border-radius: 10px;
	}
	#closeForm{
		position: absolute;
		top: 1%;
		right: 1%;
	}
	
</style>


