<template>
	<div class="addField" style="width: 90%;margin-left: 10%">
		<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
  <el-form-item label="场地名称" prop="fieldname">
    <el-input v-model="ruleForm.fieldname" style="width: 75%"></el-input>
  </el-form-item>
  <el-form-item label="活动区域" prop="fieldinout">
    <el-select v-model="ruleForm.fieldinout" placeholder="请选择活动区域">
      <el-option label="室内" value="室内"></el-option>
      <el-option label="室外" value="室外"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="场地类型" status-icon prop="fieldtype">
    <el-select v-model="ruleForm.fieldtype" placeholder="请选择场地类型">
      <el-option label="羽毛球场" value="羽毛球场"></el-option>
      <el-option label="游泳池" value="游泳池"></el-option>
      <el-option label="篮球场" value="篮球场"></el-option>
      <el-option label="乒乓球" value="乒乓球台"></el-option>
      <el-option label="桌球" value="桌球台"></el-option>
      <el-option label="网球" value="网球场"></el-option>
      <el-option label="排球" value="排球场"></el-option>
      <el-option label="足球" value="足球场"></el-option>
    </el-select> 
  </el-form-item>
  <el-form-item label="图片地址" prop="fieldimg" status-icon>
  	<el-select v-model="ruleForm.fieldimg" placeholder="请选择对应图片">
		<el-option label="室内羽毛球场" value="static/img/iBadminton.jpg"></el-option>
		<el-option label="室外羽毛球场" value="static/img/oBadminton.jpg"></el-option>
		<el-option label="室内游泳池" value="static/img/1.jpg"></el-option>
		<el-option label="室外游泳池" value="static/img/2.jpg"></el-option>
		<el-option label="室内篮球场" value="static/img/iBasketball.jpg"></el-option>
		<el-option label="室外篮球场" value="static/img/oBasketball.jpg"></el-option>
		<el-option label="室内乒乓球" value="static/img/iTableTennis.jpg"></el-option>
		<el-option label="桌球" value="static/img/billiards.jpg"></el-option>
		<el-option label="网球" value="static/img/oTennis.jpg"></el-option>
		<el-option label="室内排球" value="static/img/iVolleyball.jpg"></el-option>
		<el-option label="室外排球" value="static/img/oVolleyball.jpg"></el-option>
		<el-option label="足球" value="static/img/oFootball.jpg"></el-option>
	</el-select>
  </el-form-item>
	<div>
		<el-image :src="ruleForm.fieldimg" style="margin-top:10px;width: 400px;height: 200px" lazy>
	      <div slot="error" class="image-slot">
	        <i class="el-icon-picture-outline"></i>
	      </div>
	    </el-image>
	</div>
  </el-form-item>
  <el-form-item label="场地大小" required>
  	<el-col :span="8">
  	<el-form-item prop="fieldlong">
  		<span style="margin-left: 1%;margin-right: 1%">米(长)&nbsp;:&nbsp;</span>
    	<el-input v-model.number="ruleForm.fieldlong" placeholder="场地长度" ></el-input>
  	</el-form-item>
  	</el-col>
  	<el-col :span="2">&nbsp;</el-col>
  	<el-col :span="8">	
  	<el-form-item prop="fieldwidth">
    <span style="margin-left: 1%;margin-right: 1%">米(宽)&nbsp;:&nbsp;</span>
    <el-input v-model.number="ruleForm.fieldwidth" placeholder="场地宽度" ></el-input>
	</el-form-item>
	</el-col>
  </el-form-item>
  <el-form-item label="状态" prop="fieldstate">
    <el-radio-group v-model="ruleForm.fieldstate">
      <el-radio label="可借用"></el-radio>
      <el-radio label="被租赁"></el-radio>
      <el-radio label="维护中"></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="场地位置" prop="address">
    <el-input type="textarea" v-model="ruleForm.address" style="width: 75%"></el-input>
  </el-form-item>
  <el-form-item label="单价设置(30分钟)" prop="unitprice">
  	<el-input type="text" v-model.number="ruleForm.unitprice" style="width:75%"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
    <el-button @click="resetForm('ruleForm')">重置</el-button>
  </el-form-item>
</el-form>
	</div>
</template>
<script>
	import axios from 'axios'
	import qs from 'qs'

  export default {
    data() {
    	var checkLW = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('不能为空'));
        }
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error('请输入数字值'));
          } else {
              callback();
          }
        }, 1000);
      };

      return {
        ruleForm: {
			fieldimg:'',	
			fieldtype:'',
			fieldinout:'',
			fieldstate:'',
			fieldlong:'',
			fieldwidth:'',
			fieldname:'',
			address:'',
			unitprice:''

        },
        rules: {
          fieldname: [
            { required: true, message: '请输入场地名称', trigger: 'blur' },
            { min: 0, max: 20, message: '长度在 0 到 20 个字符', trigger: 'blur' }
          ],
          fieldinout: [
            { required: true, message: '请选择活动区域', trigger: 'change' }
          ],
          fieldtype:[
          	{required:true,message:'请选择场地类型',trigger:'change'}
          ],
		  fieldlong:[
		  	{validator:checkLW,trigger:'blur'}
		  ],
		  fieldwidth:[
		  	{validator:checkLW,trigger:'blur'}
		  ],
          fieldstate: [
            { required: true, message: '请选择状态', trigger: 'change' }
          ],
          address: [
            { required: true, message: '请填写地址', trigger: 'blur' }
          ],
          fieldimg:[
          	{ required: true, message: '请填写图片地址', trigger: 'change' }
          ],
          unitprice:[
          	{validator:checkLW,trigger:'blur'}
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
        		const loading = this.$loading({
		          lock: true,
		          text: 'Loading',
		          spinner: 'el-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        });
          if (valid) {
             //console.log(this.ruleForm);
				var control = false;
				axios({
					url:'http://localhost:8080/GMS/field/addField.do',
					method:'post',
					headers:{
		  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
						'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
  					},
					data:qs.stringify(this.ruleForm)
				}).then(resp => {
					//console.log(this.ruleForm);
					var tempC = resp.data;
					//console.log(tempC);
					if (tempC==='Success to AddField') {
						control=true;
					}
					if (control) {
						setTimeout(() => {
							loading.close();
							this.$notify({
					          title: '成功',
					          message: '您新增了一个场地',
					          position:'bottom-right',
					          type: 'success'
				        	});
							return true;
						},1000);
						//置为默认值
						this.ruleForm={
								fieldimg:'',	
								fieldtype:'',
								fieldinout:'',
								fieldstate:'',
								fieldlong:'',
								fieldwidth:'',
								fieldname:'',
								address:'',
								unitprice:''
        					}
        					//重置表单判断
        					this.$refs[formName].resetFields();
						control=false;
					}else{
						console.log('err')
					}
				})
				
				
          } else {
            console.log('error submit!!');
            
            setTimeout(() => {
            	loading.close();
            	this.$notify({
		          title: '失败',
		          message: '您的信息有误',
		          position:'bottom-right',
		          type: 'error'
		        });
		        return false;
            },300);
            
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style>
	
</style>


