import Vue from 'vue'
import Router from 'vue-router'

//路由模板导入
import HomePage from '../components/modules/body/Home.vue'
import InsidePage from '../components/modules/bodyField/showField/outOrin/Inside.vue'
import OutsidePage from '../components/modules/bodyField/showField/outOrin/Outside.vue'
import MessageCenter from '../components/modules/messageCenter/MessageCenter.vue'
import ResolutionCenter from '../components/modules/resolutionCenter/ResolutionCenter.vue'
import AddField from '../components/modules/adminControl/AddField.vue'
import AddBlackList from '../components/modules/blackList/AddBlackList.vue'
import DelBlackList from '../components/modules/blackList/DeleteBlackList.vue'
import ChangeBlackList from '../components/modules/blackList/ChangeBlackList.vue'
import Available from '../components/modules/bodyField/orderField/Available.vue'
import Borrowed from '../components/modules/bodyField/orderField/Borrowed.vue'
import Maintenance from '../components/modules/bodyField/orderField/Maintenance.vue'
import DeleteField from '../components/modules/adminControl/DeleteField.vue'
import ChangeField from '../components/modules/adminControl/ChangeField.vue'
import AddNotice from '../components/modules/Notice/AddNotice.vue'
import DeleteNotice from '../components/modules/Notice/DeleteNotice.vue'
import ChangeNotice from '../components/modules/Notice/ChangeNotice.vue'
import AddRegulation from '../components/modules/regulation/AddRegulation.vue'
import DeleteRegulation from '../components/modules/regulation/DeleteRegulation.vue'
import ChangeRegulation from '../components/modules/regulation/ChangeRegulation.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'',
      redirect:'/Home'
    },
	  {
	  	//路由实例
  	path:'/Home',
  	//注意component没有s
  	component:HomePage
	  },
    {
      path:'/Field/FieldShow/InSide',
      component:InsidePage
    },
    {
      path:'/Field/FieldShow/Outside',
      component:OutsidePage
    },
    {
      path:'/MessageCenter',
      component:MessageCenter
    },
    {
      path:'/ResolutionCenter',
      component:ResolutionCenter
    },
    {
      path:'/adminControl/AddField',
      component:AddField
    },
    {
      path:'/BlackList/Add',
          component:AddBlackList
    },
    {
      path:'/BlackList/Del',
          component:DelBlackList
    },
    {
      path:'/BlackList/Change',
          component:ChangeBlackList
    },
    {
      path:'/Field/FieldOrder/Available',
      component:Available
    },
    {
      path:'/Field/FieldOrder/Borrowed',
      component:Borrowed
    },
    {
      path:'/Field/FieldOrder/Maintenance',
      component:Maintenance
    },
    {
      path:'/adminControl/DeleteField',
      component:DeleteField
    },
    {
      path:'/adminControl/ChangeField',
      component:ChangeField
    },
    {
    	path:'/Notice/Add',
    	component:AddNotice
    },
    {
    	path:'/Notice/Del',
    	component:DeleteNotice
    },
    {
    	path:'/Notice/Change',
    	component:ChangeNotice
    },
    {
      path:'/Regulation/Add',
      component:AddRegulation
    },
    {
      path:'/Regulation/Del',
      component:DeleteRegulation
    },
    {
      path:'/Regulation/Change',
      component:ChangeRegulation
    }
	  //   	component:resolve => require(['../components/modules/body/Home.vue'],resolve)懒加载的使用
  ]
  /*
  路由名字配置：改短路由名字
  linkActiveClass:'active'
   */
})



