<template>
	<div id="head">
		<div id="navBar">
			<div id="flexChild" style="padding-top: 0.3%;margin-right: 1%">
				<el-tooltip placement="bottom" :content="content" style="z-index: 500">
					<el-button style="height: 30px;line-height: 1px" :type="btnType" icon="el-icon-s-check" @click="jumpToUser">{{valuename}}</el-button>
				</el-tooltip>
				<el-tooltip placement="bottom" content="您要跳转到赛事管理吗?">
						<el-button icon="el-icon-s-ticket" style="height: 30px;line-height: 1px" type="danger" @click="jumpToEvent">赛事</el-button>
				</el-tooltip>
				<el-tooltip placement="bottom" content="您要跳转器材管理吗?">
					<el-button style="height: 30px;line-height: 1px" type="danger" icon="el-icon-basketball" @click="jumpToequipment">器材</el-button>
				</el-tooltip>
			</div>
		</div>
		<div>
			<el-container style="margin-top: -1%">
	  			<el-header class="header" style="height:40px">
					<!-- el-icon-s-custom  el-icon-user -->
					<div id="flexParent">
						<!-- 无边框带图片文字按钮 -->
						
					</div>
	  			</el-header>
	  			<el-main class="main">
	  				<NavMenu></NavMenu>
	  			</el-main>
			</el-container>
		</div>
	</div>
</template>

<script>
	import NavMenu from './NavMenu.vue'

	export default {
		data(){
			return {
				valuename:'登录注册',
				btnType:'danger',
				content:'你要进行登录注册操作吗？',
				content1:'你要进行登录注册操作吗？',
				content2:'个人中心',
				uName:'',
				uID:'',
				pID:''
			}
		},
		components:{
			NavMenu
		},
		methods:{
			jumpToEvent(){
				window.location.href="/GMS/event/event_welcome.html"
			},
			jumpToequipment(){
				window.location.href="/GMS/equip/qcgl.html"
			},
			jumpToUser(){
				//window.location.href="/GMS/"
				//
				if (this.pID!='') {
					window.location.href="/GMS/user/PersonalCenter.html"
				}else{
					window.location.href="/GMS/user/home.html"
				}
			},

			booleanUser(){
				//var uID=1;
				//var uName='张三';
				//var pID=2;
				//this.$setCookie('UName',uID+';'+uName+';'+pID,1)
				//
				
				let UsrMsg = this.$getCookie('UName');
				var uName = '';
				var uID = '';
				var pID = '';
				if (UsrMsg!='') {
				uName = UsrMsg[1];
				this.uID = parseInt(uID);
				this.pID = parseInt(pID);
					if (uName.length>0&&uName!='') {
							this.btnType = 'info';
							this.valuename = uName;
							this.content=this.content2;
					}
				}else{
					this.btnType='danger';
					this.valuename='登录注册';
					this.content=this.content1;
				}
			}


		},
		created(){
			this.booleanUser()
		},
		computed:{

		}
	}
</script>

<style scoped>
/deep/ #navBar{
		height: 50px;
		background-color:#18367C;
		width: 104.3%;
		margin-left: -3%;
		padding-top: 10px;
	}
	/deep/ .el-button{
		padding-top: 8px;
		font-size: 12px;
	}
	.main{
		height: 170px;
	}
	#flexParent{
		/*主轴，子元素向右对齐
		*/
		display: flex;
		align-items: center;

	}
	#flexChild{
		flex: 1;
		text-align: right;
	}
</style>


