<template>
	<div class="borrowed">
		<el-row :gutter="10">
		  <el-col 
		  :sm="12" 
		  :md="8" 
		  :lg="6" 
		  :xl="4" 
		  v-for="(site,index) in sites.slice((currentPage-1)*(nowPageNum),currentPage*nowPageNum)"
		  :key="index"
		  >
		  	<div class="grid-content bg-purple">
		  		<el-image :src="site.fieldimg" fit="fill" lazy style="width: 200px;height: 200px">
		  			<div slot="error" class="image-slot">
        				<i class="el-icon-picture-outline"></i>
      				</div>
		  		</el-image>
		  		<div style="width: 27%;height:200px; float: right;">
		  			<div style="height: 120px">
		  				<p>
	           			 	<h2>详细:</h2>
								<br>
	           			 	{{site.address}}...
			  			</p>
		  			</div>
		  			
            		<el-button type="text" @click="showField(index,site)" style="margin-left: 30px;margin-bottom: -10px">查看<i class="el-icon-view el-icon--right"></i></el-button>		
            		<el-button type="primary" :disabled="flag" style="width:80%">预约<i class="el-icon-s-order el-icon--right"></i></el-button>			            	
            	<!-- @click="Fmsg(index)" -->
            </div>
		  		<!-- <img :src="site.InsUrl" alt="图片未加载" style="width: 200px;height: 200px"> -->
		  	</div>
		 </el-col>
		</el-row>

		<el-dialog lock-scroll title="查看" :visible.sync=dialogFormVisible :close-on-press-escape=false :show-close=false>
			<div style="height:550px">
        <el-card shadow="hover" style="height:350px;margin-top:-30px"body-style="{ padding: '0px' }">
				<el-image :src="tableSite.fieldimg" fit="fill" lazy style="width: 100%;height: 20%">
		  			<div slot="error" class="image-slot">
        				<i class="el-icon-picture-outline"></i>
      				</div>
		  		</el-image>
        </el-card>
		  		<div style="margin-top:20px;">
          <el-card shadow="hover" style="padding-left:20%;margin-top: 3px;height:200px"body-style="{ padding: '0px' }">
					<span style="font-size:16px">场地名字:{{tableSite.fieldname}}</span><br>
   			 	<span style="font-size:16px">场地类型：{{tableSite.fieldtype}}</span><br>
   			 	<span style="font-size:16px">场地状态：{{tableSite.fieldstate}}</span><br>
					<span style="font-size:16px">场地长度：&nbsp;&nbsp;米</span><br>
					<span style="font-size:16px">场地宽度：{{tableSite.fieldwidth}}&nbsp;&nbsp;米</span><br>
					<span style="font-size:16px">场地位置：{{tableSite.address}}</span><br>
					<span style="font-size:16px">场地租赁价格(每小时)：￥{{tableSite.unitprice}}</span><br>
          </el-card>  
				</div>
		</div>
		</el-dialog>
		<div class="block" style="text-align: center;margin-top: 20px">
		    <el-pagination
		      @size-change="handleSizeChange"
		      @current-change="handleCurrentChange"
		      :current-page="currentPage"
		      :page-sizes="pageSizes"
		      layout="total, sizes, prev, pager, next, jumper"
		      :total="totalnum">
		    </el-pagination>
 		</div>	
	</div>
</template>

<script>
	import qs from 'qs'
	import axios from 'axios'
	export default {
		data(){
			return {
				dialogFormVisible:false,
				tableSite:{
						fieldimg:'',
						fieldtype:'',
						fieldinout:'',
						fieldstate:'',
						fieldlong:'',
						fieldwidth:'',
						fieldname:'',
						address:'',
						fieldimg:'',
						unitprice:''
					},
				// InsUrl:require('../../../../../assets/2.jpg')
					sites:[
						{
						fieldimg:'',
						fieldtype:'',
						fieldinout:'',
						fieldstate:'',
						fieldlong:'',
						fieldwidth:'',
						fieldname:'',
						address:'',
						fieldimg:'',
						unitprice:''
						}
					],
					currentPage:1,	//当前页码
					totalnum:0,		//总量设定初值为零，在Vue的中钩子函数onCreate()中进行axios的ajax请求，获取的数量进行赋值操作，如果请求失败，显示为零显然符合需求
					pageNum:1,		//初始页码
					pageSizes:[20,40],	//每页数据
					nowPageNum:20,	//默认初始值为20	
					flag:true
			}
		},
		methods:{
			handleSizeChange(val){
				this.nowPageNum=`${val}`;
				//console.log(`每页${val}条`);

			},
			 handleCurrentChange(val) {
        		//console.log(`当前页: ${val}`);
        		this.currentPage=parseInt(`${val}`);
        		//console.log(this.currentPage+' '+this.pageSizes);
     		 },

     		 handleSites(){
     		 	//请求发送
     		 	axios({
						url:'http://localhost:8080/GMS/field/getVisibleField.do',
						method:'post',
						data:qs.stringify({'fieldState':'被租赁'}),
						headers:{
							'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
							'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
						},
					}).then(resp => {
						this.sites = resp.data;
						this.totalnum=this.sites.length;
					//console.log(resp.data);
				}).catch(error =>{
					console.log('err');
				})
     		 },
     		 showField(index,obj){
     		 	this.dialogFormVisible=true;
     		 	this.tableSite=obj;
     		 }
		},
		created(){
			this.handleSites();

		},
	}
</script>

<style scoped>
	.el-row{
		margin-top: 10px;
	}
	.el-col {
		padding-top: 10px;
		border-radius: 4px;
	}
	.bg-purple-dark {
		background: #99a9bf;
	}
	.bg-purple {
		background: #d3dce6;
	}
	.bg-purple-light {
		background: #e5e9f2;
	}
	.grid-content {
		border-radius: 4px;
		min-height: 36px;
	}
</style>


