<template>
	<div class="messagecenter">
		<el-collapse v-model="activeNames" accordion >
			<el-collapse-item 
			name="1"
			>
			<template slot="title">
				我的场地预约&nbsp;&nbsp;&nbsp;<i class="el-icon-message"></i>
				<el-badge :value="morderLength" class="item">
				</el-badge>
			</template>
				<div id="fileOrder">
					<MessageOrder @MorderLength="getMorderLength"></MessageOrder>
				</div>
			</el-collapse-item>
			<el-collapse-item name="2">
				<template slot="title">
				公告信息查询&nbsp;&nbsp;&nbsp;<i class="el-icon-search"></i>
				<el-badge is-dot class="item">
				</el-badge>
				</template>
				<div>
					<msgAnno></msgAnno>
				</div>
			</el-collapse-item>
			<el-collapse-item name="3">
				<template slot="title">
						历史借用场地记录&nbsp;&nbsp;&nbsp;<i class="el-icon-time"></i>
					<el-badge :value="horderLength" class="item">
					</el-badge>
				</template>
				<div>
					<HistoryOrder @HorderLength="getHorderLength"></HistoryOrder>
				</div>
			</el-collapse-item>
			<el-collapse-item name="4">
				<template slot="title">
						接收信息&nbsp;&nbsp;&nbsp;<i class="el-icon-chat-dot-round"></i>
					<el-badge :value="msgGetLength" class="item">
					</el-badge>
				</template>
				
			</el-collapse-item>
			<el-collapse-item>
				<template slot="title">
						已支付订单&nbsp;&nbsp;&nbsp;<i class="el-icon-wallet"></i>
					<el-badge :value="porderLength" class="item">
					</el-badge>
				</template>
				<div>
					<PaidOrder @PorderLength="getPorderLength"></PaidOrder>
				</div>
			</el-collapse-item>
		</el-collapse>
	</div>
</template>

<script>
	import MessageOrder from './MessageItem/MessageOrder.vue'
	import MsgAnno from './MessageItem/MessageAnno.vue'
	import HistoryOrder from './MessageItem/HistoryOrder.vue'
	import PaidOrder from './MessageItem/PaidOrder.vue'
	export default {
		data(){
			return {
				activeNames:'1',
				morderLength:0,
				horderLength:0,
				porderLength:0,
				msgGetLength:0
			}
		},
		methods:{
			getMorderLength(msg){
				this.morderLength = msg;
			},
			getPorderLength(msg){
				this.porderLength = msg;
			},
			getHorderLength(msg){
				this.horderLength = msg;
			},
		},
		components:{
			MessageOrder,
			MsgAnno,
			HistoryOrder,
			PaidOrder
		}
	}
</script>

<style scoped>
	.messagecenter{

		width: 90%;
		margin-left:5%;
	}
	#fileEmail{
		width: 100%;
	}
	/deep/ .el-collapse-item__header{
		height: 100px;
		font-size: 20px;
	}
	/deep/.item {
		margin-top: -30px;
  		margin-left: 0px;
	}
</style>


