<template>
  <el-carousel :interval="4000" type="card" :height="ImgH+'px'">
    <el-carousel-item v-for="item in imgList" :key="item.id">
      <div class="medium">
        <img :src="item.idView" ref="ImgH" alt="显示错误" style="width: 100%" @load="imgLoader" > 
      </div>
    </el-carousel-item>
  </el-carousel>
</template>

<script>
      export default {
        data(){
          return {
            //图片引入
            imgList:[
              {id:0,name:'室内游泳馆',idView:require('../../../../static/img/1.jpg')},
              {id:1,name:'室内游泳池',idView:require('../../../../static/img/2.jpg')},
              {id:2,name:'桌球台',idView:require('../../../../static/img/billiards.jpg')},
              {id:3,name:'室内羽毛球场',idView:require('../../../../static/img/iBadminton.jpg')},
              {id:4,name:'室外网球场',idView:require('../../../../static/img/oTennis.jpg')},
              {id:5,name:'足球场',idView:require('../../../../static/img/oFootball.jpg')},
            ],
            ImgH:''         
          }
        },
        methods:{
          imgLoader(){
            this.$nextTick(() => {
              this.ImgH=this.$refs.ImgH[1].height;
              //console.log(this.$refs.ImgH[1].height);
            })
          }
        },
        // 渲染挂载时候调用,根据第一张图片自适应高度
        mounted(){
          this.imgLoader();
          // 增加事件监听，视口(resize)发生变化，获取赋值
          // this.$ref.name 用于普通元素获取的是DOM元素
          // 用于子组件可以获取组件实例，使用组件所有方法
          // ref在渲染时不能访问，它也是被渲染完成时创建的，因此可以在mounted中访问
          // 钩子函数的七个周期中mounted()指的是被渲染完成后进行的方法
          window.addEventListener('resize',() => {
            this.ImgH = this.$refs.ImgH[1].height;
            this.imgLoader();
          },false)
        }
      }
</script>

<style scoped>
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>


