<template>
  <div class="DeleteField">
    <el-input placeholder="请输入要查询的信息"  v-model="search" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>
    <el-table
    :data="tables"
    border
    style="width: 90%;margin-left: 5%"
    stripe>
    <el-table-column
      fixed
      prop="blacklistid"
      label="黑名单id"
      width="150%">
    </el-table-column>
    <el-table-column
      prop="regulationid"
      label="条例id"
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.regulationid)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="userid"
      label="用户id"
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.userid)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="vioreason"
      label="违反条例原因"
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.vioreason)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="solution"
      label="处理"
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.solution)"></span>
      </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      style="text-align: center"
      width="100%">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="deleteRow(scope/*scope.$index*/, tableData,tables)"
          type="danger"
          size="mini">
          移除
        </el-button>
      </template>
    </el-table-column>
  </el-table>

<div style="margin-top:120px;color:#F00;font-size:30px;margin-bottom:60px">恢复公告状态</div>

<el-input placeholder="请输入要查询的信息"  v-model="searcho" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>
    <el-table
    :data="tableso"
    border
    style="width: 90%;margin-left: 5%"
    stripe>
    <el-table-column
      fixed
      prop="blacklistid"
      label="黑名单id"
      width="150%">
    </el-table-column>
    <el-table-column
      prop="regulationid"
      label="条例id"
      width="300%">
      <template slot-scope="scope">
        <span v-html="formato(scope.row.regulationid)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="userid"
      label="用户id"
      width="300%">
      <template slot-scope="scope">
        <span v-html="formato(scope.row.userid)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="vioreason"
      label="违反条例原因"
      width="300%">
      <template slot-scope="scope">
        <span v-html="formato(scope.row.vioreason)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="solution"
      label="处理"
      width="300%">
      <template slot-scope="scope">
        <span v-html="formato(scope.row.solution)"></span>
      </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      style="text-align: center"
      width="200%">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="recycleRow(scope/*scope.$index*/)"
          type="danger"
          size="mini">
          回收
        </el-button>
      </template>
    </el-table-column>
  </el-table>

  </div>
</template>

<script>

export default {

  data () {
    return {
      search: '',
      searcho:'',
      tableData: [
        {
            blacklistid:'1',
            regulationid:'2',
            userid:'',
            vioreason:'abc',
            solution:'def'
          }
      ],
      tableDatao: [
        {
            blacklistid:'1',
            regulationid:'2',
            userid:'',
            vioreason:'abc',
            solution:'def'
          }
      ]
    }
  },

  computed: {
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }else{
        return this.tableData;
      }
    },
    tableso() {
      const searcho = this.searcho
      if (searcho) {
        return this.tableDatao.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(searcho) > -1
          })
        })
      }else{
        return this.tableDatao;
      }
    }
  },

  methods: {
     deleteRow(scope, rows,searchTable) {
       
      },
    recycleRow(scope){
    	console.log('recycle');
    },
    format (val) {
      if (val.indexOf(this.search) !== -1 && this.search !== '') {
        return val.replace(this.search, '<font color="red">' + this.search + '</font>')
      } else {
        return val;
      }
    },
      formato (val) {
      //val.indexOf(this.search) !== -1 &&
      if (val.indexOf(this.searcho)!==-1&& this.searcho !== '') {
        return val.replace(this.searcho, '<font color="red">' + this.searcho + '</font>')
      } else {
        return val;
      }
    },
  }

}
</script>

<style scoped>

</style>


