<template>
  <div class="DeleteField">
    <el-input placeholder="请输入要查询的信息"  v-model="search" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>
    <el-table
    :data="tables"
    border
    style="width: 90%;margin-left: 5%"
    stripe>
<el-table-column
    prop="fieldid"
    label="场地ID"
    width="150%"
    fixed>
    </el-table-column>
    <el-table-column
      prop="fieldname"
      label="场地名称"
      width="150%">
    <template slot-scope="scope">
      <span v-html="format(scope.row.fieldname)"></span>
    </template>
    </el-table-column>
    <el-table-column
      prop="fieldtype"
      label="类型"
      width="200%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.fieldtype)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="fieldstate"
      label="场地状态"
      width="200%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.fieldstate)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="fieldwidth"
      label="场地宽度"
      width="200%">
    </el-table-column>
    <el-table-column
      prop="fieldlong"
      label="场地长度"
      width="200%">
    </el-table-column>
        <el-table-column
      prop="unitprice"
      label="场地单价(元)"
      width="200%">
    </el-table-column>
    <el-table-column
      prop="address"
      label="场地位置"
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.address)"></span>
      </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      style="text-align: center"
      width="100%">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="changeRow(scope/*scope.$index*/, tableData,tables)"
          type="danger"
          size="mini">
          修改
        </el-button>
      </template>
    </el-table-column>
  </el-table>
  
  <el-dialog title="修改" :visible.sync="dialogFormVisible" fullscreen :close-on-press-escape=false :show-close=false>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
	<el-form-item label="场地ID" prop="fieldid">
		<el-input v-model="ruleForm.fieldid" disabled style="width: 75%"></el-input>
	</el-form-item>
  <el-form-item label="场地名称" prop="fieldname">
    <el-input v-model="ruleForm.fieldname" style="width: 75%"></el-input>
  </el-form-item>
  <el-form-item label="活动区域" prop="fieldinout"> 
    <el-select v-model="ruleForm.fieldinout" placeholder="请选择活动区域">
      <el-option label="室内" value="室内"></el-option>
      <el-option label="室外" value="室外"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="场地类型" status-icon prop="fieldtype">
    <el-select v-model="ruleForm.fieldtype" placeholder="请选择场地类型">
      <el-option label="羽毛球场" value="羽毛球场"></el-option>
      <el-option label="游泳池" value="游泳池"></el-option>
      <el-option label="篮球场" value="篮球场"></el-option>
      <el-option label="乒乓球" value="乒乓球"></el-option>
      <el-option label="桌球" value="桌球"></el-option>
      <el-option label="网球" value="网球"></el-option>
      <el-option label="排球" value="排球"></el-option>
      <el-option label="足球" value="足球"></el-option>
    </el-select> 
  </el-form-item>
  <el-form-item label="图片地址" prop="fieldimg" status-icon>
  <el-form-item label="图片地址" prop="fieldimg" status-icon>
  	<el-select v-model="ruleForm.fieldimg" placeholder="请选择对应图片">
		<el-option label="室内羽毛球场" value="static/img/iBadminton.jpg"></el-option>
		<el-option label="室外羽毛球场" value="static/img/oBadminton.jpg"></el-option>
		<el-option label="室内游泳池" value="static/img/1.jpg"></el-option>
		<el-option label="室外游泳池" value="static/img/2.jpg"></el-option>
		<el-option label="室内篮球场" value="static/img/iBasketball.jpg"></el-option>
		<el-option label="室外篮球场" value="static/img/oBasketball.jpg"></el-option>
		<el-option label="室内乒乓球" value="static/img/iTableTennis.jpg"></el-option>
		<el-option label="桌球" value="static/img/billiards.jpg"></el-option>
		<el-option label="网球" value="static/img/oTennis.jpg"></el-option>
		<el-option label="室内排球" value="static/img/iVolleyball.jpg"></el-option>
		<el-option label="室外排球" value="static/img/oVolleyball.jpg"></el-option>
		<el-option label="足球" value="static/img/oFootball.jpg"></el-option>
	</el-select>
  </el-form-item>
	<div>
		<el-image :src="ruleForm.fieldimg" style="margin-top:10px;width: 400px;height: 200px" lazy>
	      <div slot="error" class="image-slot">
	        <i class="el-icon-picture-outline"></i>
	      </div>
	    </el-image>
	</div>
  </el-form-item>
  <el-form-item label="场地大小" required>
    <el-col :span="8">
    <el-form-item prop="fieldlong">
      <span style="margin-left: 1%;margin-right: 1%">米(长)&nbsp;:&nbsp;</span>
      <el-input v-model.number="ruleForm.fieldlong" placeholder="场地长度" ></el-input>
    </el-form-item>
    </el-col>
    <el-col :span="2">&nbsp;</el-col>
    <el-col :span="8">  
    <el-form-item prop="fieldwidth">
    <span style="margin-left: 1%;margin-right: 1%">米(宽)&nbsp;:&nbsp;</span>
    <el-input v-model.number="ruleForm.fieldwidth" placeholder="场地宽度" ></el-input>
  </el-form-item>
  </el-col>
  </el-form-item>
  <el-form-item label="状态" prop="fieldstate">
    <el-radio-group v-model="ruleForm.fieldstate">
      <el-radio label="可借用" value="可借用"></el-radio>
      <el-radio label="被租赁" value="被租赁"></el-radio>
      <el-radio label="维护中" value="维护中"></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="场地每30分钟单价(元)" prop="unitprice">
    <el-input type="text" v-model.number="ruleForm.unitprice" style="width: 72%"></el-input>
  </el-form-item>
  <el-form-item label="场地位置" prop="address">
    <el-input type="textarea" v-model="ruleForm.address" style="width: 75%"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
    <el-button type="info" @click="cancelForm('ruleForm')">取消修改</el-button>
  </el-form-item>
</el-form>
  </el-dialog>
  </div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'

export default {
  data () {
    var checkLW = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('不能为空'));
        }
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error('请输入数字值'));
          } else {
              callback();
          }
        }, 1000);
      };
    return {
      search: '',
      TempValue:{
        fieldid:'',
        fieldimg:'',	
        fieldtype:'',
        fieldinout:'',
        fieldstate:'',
        fieldlong:'',
        fieldwidth:'',
        fieldname:'',
        address:'',
        unitprice:''
      },
      ruleForm: {
    		fieldid:'',
    		fieldimg:'',	
    		fieldtype:'',
    		fieldinout:'',
    		fieldstate:'',
    		fieldlong:'',
    		fieldwidth:'',
    		fieldname:'',
    		address:'',
    		unitprice:''
        },
      tableDataL:6,
      dialogFormVisible:false,
      tableData: [
        
        {
			fieldid:'',
			fieldimg:'',	
			fieldtype:'',
			fieldinout:'',
			fieldstate:'',
			fieldlong:'',
			fieldwidth:'',
			fieldname:'',
			address:'',
			unitprice:''
		}
      ],
      rules: {
          fieldname: [
            { required: true, message: '请输入场地名称', trigger: 'blur' },
            { min: 0, max: 10, message: '长度在 0 到 10 个字符', trigger: 'blur' }
          ],
          fieldinout: [
            { required: true, message: '请选择活动区域', trigger: 'change' }
          ],
          fieldtype:[
            {required:true,message:'请选择场地类型',trigger:'change'}
          ],
		  fieldlong:[
        	{validator:checkLW,trigger:'blur'}
      ],
      	  fieldwidth:[
        	{validator:checkLW,trigger:'blur'}
      ],
          fieldstate: [
            { required: true, message: '请选择状态', trigger: 'change' }
          ],
          address: [
            { required: true, message: '请填写地址', trigger: 'blur' }
          ],
          unitprice:[
          	{validator:checkLW,trigger:'blur'}
          ]
        }
    }
  },
  computed: {
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }else{
        return this.tableData;
      }
    }
  },

  methods: {

     changeRow(scope, rows,searchTable) {
      var temp={
        fieldid:'',
		fieldimg:'',	
		fieldtype:'',
		fieldinout:'',
		fieldstate:'',
		fieldlong:'',
		fieldwidth:'',
		fieldname:'',
		address:''
      };
      if (scope.row.fieldid!='') {
       //console.log('$index:'+scope.$index+'     row.tIndex:+scope.row.tIndex'+'     Datalength:'+rows.length+'     Searchlength:'+searchTable.length+"               this.search !== '':"+(this.search !== ''));
       this.dialogFormVisible=true;
       temp=JSON.parse(JSON.stringify(scope.row));
       this.TempValue=temp;
       this.ruleForm=scope.row;
       // 暂时存入
      	
      }else{
      	this.$message.error('错了哦，接受不了后台数据啦！请重新刷新');
      }
       

       
      },
		allSite(){
			axios({
				url:'http://localhost:8080/GMS/field/getAllField.do',
				method:'post',
				headers:{
					'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
					'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
				},
			}).then(resp => {
				this.tableData = resp.data;
			}).catch(error =>{
				console.log('err');
			})
		},
    format (val) {
      if (val.indexOf(this.search) !== -1 && this.search !== '') {
        return val.replace(this.search, '<font color="red">' + this.search + '</font>')
      } else {
        return val;
      }
    },
    submitForm(formName) {
        //console.log(JSON.stringify(this.TempValue)==JSON.stringify(this.ruleForm));
        this.$refs[formName].validate((valid) => {
            const loading = this.$loading({
              lock: true,
              text: 'Loading',
              spinner: 'el-icon-loading',
              background: 'rgba(0, 0, 0, 0.7)'
            });
            //验证数据是否更改，不做无谓操作!!
          if (valid&&JSON.stringify(this.TempValue)!=JSON.stringify(this.ruleForm)) {
             var control = false;
             axios({
             	url:'http://localhost:8080/GMS/field/changeField.do',
					method:'post',
					headers:{
		  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
						'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
  					},
					data:qs.stringify(this.ruleForm)
             }).then(resp => {
             	var tempC = resp.data;
             	if (tempC==='Success to ChangeField') {
						control=true;
					}
             	if (control) {
					setTimeout(() => {
						loading.close();
						this.$notify({
						title: '成功',
						message: '您修改了一个场地',
						position:'bottom-right',
						type: 'success'
					});
						this.dialogFormVisible=false;
						return true;
					},1000);
					control=false;
             	}
             }).catch(err => {
             	console.log('err');
             })
        
          } else if (valid&&JSON.stringify(this.TempValue)==JSON.stringify(this.ruleForm)) {
				setTimeout(() => {
					loading.close();
					this.$notify({
						title: '警告',
						message: '数据没有变化，请检查',
						position:'bottom-right',
						type: 'warning'
					});
						return false;
					},300);
          }else {
            console.log('error submit!!');
            
            setTimeout(() => {
              loading.close();
              this.$notify({
              title: '失败',
              message: '您的信息有误',
              position:'bottom-right',
              type: 'error'
            });
            return false;
            },300);
            
          }
        });
      },
      cancelForm(formName){
        //点击取消恢复之前的值
        this.ruleForm=this.TempValue;/*JSON.parse(JSON.stringify(this.TempValue));*/
          this.$refs.ruleForm.resetFields();
          this.dialogFormVisible=false;
        
        
      }
  },
  created(){
  	this.allSite();
  }
}

</script>

<style scoped>

</style>



