<template>
	<div id="block" style="height:70%; width:90%; margin-left:5%">
		<el-container style="height: 500px; border: 1px solid #eee">
  <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
    <el-menu>
       <el-menu-item>重大赛事场地预约</el-menu-item>
    </el-menu>
  </el-aside>
  
  <el-container>
    
    <el-main>
      <el-input placeholder="请输入要查询的信息"  v-model="search" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>
    <el-table
    :data="tables"
    border
    style="width: 90%;margin-left: 5%"
    stripe>
<el-table-column
    prop="fieldid"
    label="场地ID"
    width="70%"
    fixed>
    </el-table-column>
    <el-table-column
      prop="fieldname"
      label="场地名称"
      width="150%">
    <template slot-scope="scope">
      <span v-html="format(scope.row.fieldname)"></span>
    </template>
    </el-table-column>
    <el-table-column
      prop="fieldtype"
      label="类型"
      width="200%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.fieldtype)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="fieldstate"
      label="场地状态"
      width="200%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.fieldstate)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="fieldwidth"
      label="场地宽度"
      width="200%">
    </el-table-column>
    <el-table-column
      prop="fieldlong"
      label="场地长度"
      width="200%">
    </el-table-column>
    <el-table-column
      prop="address"
      label="场地位置"
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.address)"></span>
      </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      style="text-align: center"
      width="150%">
      <template slot-scope="scope">
<!--         <el-button
  @click.native.prevent="changeRow(scope/*scope.$index*/, tableData,tables)"
  type="danger"
  size="mini">
  修改
</el-button> -->
			<el-button
				@click="check(scope,tableData)"
				type="primary"
				size="mini"
				id="checkF"
				>
				查看
			</el-button>
			<el-button
				@click="orderEvent(scope)"
				type="danger"
				size="mini"
				id="btnOrder"
				style="width: 60px"
				>
				预约场地
			</el-button>
      </template>
    </el-table-column>
  </el-table>
    </el-main>
  </el-container>
</el-container>

 		<el-dialog lock-scroll title="查看" :visible.sync=dialogFormVisible :close-on-press-escape=false :show-close=false>
      <div style="height:550px">
        <el-card shadow="hover" style="height:350px;margin-top:-30px"body-style="{ padding: '0px' }">
        <el-image :src="tableSite.fieldimg" fit="fill" lazy style="width: 100%;height: 20%">
            <div slot="error" class="image-slot">
                <i class="el-icon-picture-outline"></i>
              </div>
          </el-image>
        </el-card>
          <div style="margin-top:20px;">
          <el-card shadow="hover" style="padding-left:20%;margin-top: 3px;height:200px"body-style="{ padding: '0px' }">
          <span style="font-size:16px">场地名字:{{tableSite.fieldname}}</span><br>
          <span style="font-size:16px">场地类型：{{tableSite.fieldtype}}</span><br>
          <span style="font-size:16px">场地状态：{{tableSite.fieldstate}}</span><br>
          <span style="font-size:16px">场地长度：{{tableSite.fieldlong}}&nbsp;&nbsp;米</span><br>
          <span style="font-size:16px">场地宽度：{{tableSite.fieldwidth}}&nbsp;&nbsp;米</span><br>
          <span style="font-size:16px">场地位置：{{tableSite.address}}</span><br>
          <span style="font-size:16px">场地租赁价格(30分钟)：￥{{tableSite.unitprice}}</span><br>
          </el-card>  
        </div>
    </div>
    </el-dialog>

    <el-dialog title="预约" lock-scroll :visible.sync=orderFormVisible fullscreen :close-on-press-escape=false :show-close=false>
      <el-form :model="form" ref="form" style="margin-left:25%" status-icon :rules="rules">
        <el-form-item label="场地ID" label-width="100px">
          <el-input v-model="form.fieldid" disabled style="width:58%"></el-input> 
        </el-form-item>
        <el-form-item label="场地名称" label-width="100px">
          <el-input v-model="fieldname" disabled style="width:58%"></el-input> 
        </el-form-item>
        <el-form-item label="userid" label-width="100px">
          <el-input v-model="form.userid" disabled autocomplete="off" style="width:58%"></el-input>
        </el-form-item>
        <el-form-item label="申请人" label-width="100px">
          <el-input v-model="name" disabled style="width:58%"></el-input>
        </el-form-item>
        <el-form-item label="联系电话" label-width="100px" prop="phone">
          <el-input v-model="form.phone" style="width:58%"></el-input>
        </el-form-item>
        <el-form-item label="场地单价" label-width="100px">
			￥<el-input v-model="form.unitprice" disabled style="width:56%"></el-input>
		</el-form-item>
        <el-form-item label="活动起始时间" label-width="100px">
          <div class="block">
            <el-date-picker
            v-model="form.startDate"
            type="date"
            format="yyyy-MM-dd"
            :default-value="form.startDate"
            value-format="timestamp"
            placeholder="选择日期"
            :picker-options="pickerOptions"
            style="width:150px">
            </el-date-picker>
            <el-time-select
            v-model="form.startTime"
            type="date"
            format="HH:mm"
            value-format="HH:mm"
            placeholder="选择时间"
            :picker-options="{
              start: '08:00',
              step: '00:15',
              end: '18:30',
              maxTime:'18:00',
              //判断是否为初始化的时间
              minTime:(form.startDate==suretime)?new Date().getHours()+':'+new Date().getMinutes():'07:45'
            }"
            style="width:150px">
            </el-time-select>
          </div>
          <span id="notification">*因为时间校准问题，日期可能需要重新选择</span>
        </el-form-item>
        <el-form-item label="活动结束时间" label-width="100px">
          <div class="block1">
            <el-date-picker
            v-model="form.endDate"
            type="date"
            format="yyyy-MM-dd"
            value-format="timestamp"
            placeholder="选择日期"
            :picker-options="pickerOptions"
            style="width:150px">
            </el-date-picker>
            <el-time-select
            v-model="form.endTime"
            type="date"
            format="HH:mm"
            value-format="HH:mm"
            placeholder="选择时间"
            :picker-options="{
              start: '08:30',
              step: '00:15',
              end: '18:30',
              minTime:(form.endDate!=='')&&(form.endDate==form.startDate)?form.startTime:'8:15'
            }"

            style="width:150px">
            </el-time-select>
          </div>
        </el-form-item>
        <el-form-item style="margin-left:25%">
          <el-button type="primary" @click="submitForm('form')" style="width:100px;font-size:14px">立即创建</el-button>
          <el-button type="info" @click="cancelForm('form')" style="width:100px;font-size:14px">取消修改</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog title="高一级预约" lock-scroll :visible.sync=orderHFormVisible fullscreen :close-on-press-escape=false :show-close=false>
      <el-form :model="form" ref="form" style="margin-left:25%" :rules="rules" status-icon>
      	<el-form-item label="需要撤销订单id" style="margin-left:-2%" label-width="120px">
      		<el-input v-model="orderTable[0].orderid" disabled style="width:58%"></el-input>
      	</el-form-item>
      	<el-form-item label="需要撤销订单用户id" style="margin-left:-4%" label-width="140px">
      		<el-input v-model="orderTable[0].userid" disabled style="width:58%"></el-input>
      	</el-form-item>
        <el-form-item label="场地ID" label-width="100px">
          <el-input v-model="form.fieldid" disabled style="width:58%"></el-input> 
        </el-form-item>
        <el-form-item label="场地名称" label-width="100px">
          <el-input v-model="fieldname" disabled style="width:58%"></el-input> 
        </el-form-item>
        <el-form-item label="userid" label-width="100px">
          <el-input v-model="form.userid" disabled autocomplete="off" style="width:58%"></el-input>
        </el-form-item>
        <el-form-item label="申请人" label-width="100px">
          <el-input v-model="name" disabled style="width:58%"></el-input>
        </el-form-item>
        <el-form-item label="联系电话" label-width="100px" prop="phone">
          <el-input v-model="form.phone" style="width:58%"></el-input>
        </el-form-item>
        <el-form-item label="场地单价" label-width="100px">
			￥<el-input v-model="form.unitprice" disabled style="width:56%"></el-input>
		</el-form-item>
        <el-form-item label="活动起始时间" label-width="100px">
          <div class="block">
            <el-date-picker
            v-model="form.startDate"
            type="date"
            format="yyyy-MM-dd"
            :default-value="form.startDate"
            value-format="timestamp"
            placeholder="选择日期"
            :picker-options="pickerOptions"
            style="width:150px">
            </el-date-picker>
            <el-time-select
            v-model="form.startTime"
            type="date"
            format="HH:mm"
            value-format="HH:mm"
            placeholder="选择时间"
            :picker-options="{
              start: '08:00',
              step: '00:15',
              end: '18:30',
              maxTime:'18:00',
              //判断是否为初始化的时间
              minTime:(form.startDate==suretime)?new Date().getHours()+':'+new Date().getMinutes():'07:45'
            }"
            style="width:150px">
            </el-time-select>
          </div>
          <span id="notification">*因为时间校准问题，日期可能需要重新选择</span>
        </el-form-item>
        <el-form-item label="活动结束时间" label-width="100px">
          <div class="block1">
            <el-date-picker
            v-model="form.endDate"
            type="date"
            format="yyyy-MM-dd"
            value-format="timestamp"
            placeholder="选择日期"
            :picker-options="pickerOptions"
            style="width:150px">
            </el-date-picker>
            <el-time-select
            v-model="form.endTime"
            type="date"
            format="HH:mm"
            value-format="HH:mm"
            placeholder="选择时间"
            :picker-options="{
              start: '08:30',
              step: '00:15',
              end: '18:30',
              minTime:(form.endDate!=='')&&(form.endDate==form.startDate)?form.startTime:'8:15'
            }"

            style="width:150px">
            </el-time-select>
          </div>
        </el-form-item>
        <el-form-item style="margin-left:25%">
          <el-button type="primary" @click="submitHForm('form')" style="width:100px;font-size:14px">立即创建</el-button>
          <el-button type="info" @click="cancelHForm('form')" style="width:100px;font-size:14px">取消修改</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
	</div>
</template>

<script>
import qs from 'qs'
import axios from 'axios'
import moment from 'moment'
  export default {
	inject:["reload"],
    data() {
        var checkLW = (rule, value, callback) => {
        	if (value==='') {
        		return callback(new Error('请输入手机号'));
        	}
        		setTimeout(() => {
        			 if (value.length!==11) {
        			 	callback(new Error('手机号位数为11位，请输入！'))
        			 }else if (!/^1(3|4|5|6|7|8|9)\d{9}$/.test(value)) {
        				callback(new Error('无效手机号或输入了特殊字符!'));
        			 }else{
        			 	callback();
        			 }
        		},1000);
        	}
      return{
        pickerOptions:{
          disabledDate(time) {
            if(time.getTime() < Date.now()||time.getTime() > Date.now()+ 3600 * 1000 * 24 * 14){
              return time.getTime()-(time.getSeconds()*1000+time.getMilliseconds());
            }
                  
                },
                shortcuts:[{
                  text: '今天',
                  onClick(picker) {
                    var time = new Date();
                    var misecond = time.getHours()*60*60*1000+time.getMinutes()*60*1000+time.getSeconds()*1000+time.getMilliseconds();
                    picker.$emit('pick', time.getTime()-misecond);
                  }
                },{
                  text: '一周后',
                  onClick(picker) {
                    const time = new Date();
                    var misecond = time.getHours()*60*60*1000+time.getMinutes()*60*1000+time.getSeconds()*1000+time.getMilliseconds();
                    time.setTime(time.getTime() + 3600 * 1000 * 24 * 7-misecond);
                    picker.$emit('pick', time);
                  }
                }]
        },
        orderFormVisible:false,
        dialogFormVisible:false,
        orderHFormVisible:false,
      	search:'',
      	orderTable:[{
      		orderid:'',
      		fieldid:'',
      		rentstart:'',
      		orderstate:'',
      		userid:'',
      		payid:''
      	}],
        tableSite:{
            fieldid:'',
            fieldtype:'',
            fieldinout:'',
            fieldstate:'',
            fieldlong:'',
            fieldwidth:'',
            fieldname:'',
            address:'',
            fieldimg:'',
            unitprice:''
          },
          form:{
            fieldid:'',
            userid:'',
            startDate:'',
            startTime:'',
            endDate:'',
            endTime:'',
            startT:'',
            endT:'',
            phone:'',
            unitprice:''
          },
      	tableData: [
        
        {
			fieldid:'',
			fieldimg:'',	
			fieldtype:'',
			fieldinout:'',
			fieldstate:'',
			fieldlong:'',
			fieldwidth:'',
			fieldname:'',
			address:'',
			unitprice:''
		}
      ],
      pID:'',
		rules:{
			fieldtype:[
				{required:true,message:'请选择场地类型',trigger:'change'}
			],
		phone:[
				{validator:checkLW,trigger:'blur'}
			]
		},
      fieldname:'',
      name:''
      }
    },
    computed: {
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }else{
        return this.tableData;
      }
    }
  },
  methods:{
  	format (val) {
      if (val.indexOf(this.search) !== -1 && this.search !== '') {
        return val.replace(this.search, '<font color="red">' + this.search + '</font>')
      } else {
        return val;
      }
    },
    check(scope,obj){
    	if(scope.row.fieldid!==''){
      		this.dialogFormVisible=true;
      		this.tableSite=scope.row;
	 	}else{
	 		this.$message.error('错了哦，接受不了后台数据啦！请重新刷新');
	 	}

    },
    fieldEvent(){
    	axios({
        url:'http://localhost:8080/GMS/field/getEventField.do',
        method:'post',
        data:qs.stringify({'fieldState':'维护中'}),
    		headers:{
    			'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
							'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
						},
    	}).then(resp => {
    		this.tableData = resp.data;
    	}).catch(err =>{
    		console.log('err');
    	})
    },
    orderEvent(scope){
          if(scope.row.fieldid!==''){
			//console.log(scope.row.fieldid);
			var data = scope.row.fieldid;
			this.form.unitprice=scope.row.unitprice;
			this.form.fieldid=scope.row.fieldid;
			//查看状态，判断是否已经被租赁
			if(scope.row.fieldstate=='被租赁'){
				//console.log('查看订单');
				this.orderHFormVisible=true;
				axios({
					url:'http://localhost:8080/GMS/field/getOneField.do',
					method:'post',
					data:qs.stringify({'fieldid':data}),
					headers:{
						'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
						'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
					},
				}).then(resp => {
					this.fieldname = resp.data.fieldname;
					//console.log(resp.data);
				}).catch(error =>{
					console.log('err');
				});

				//查询订单请求
				axios({
					url:'http://localhost:8080/GMS/FieldOrder/getOrderByfidAndState.do',
					method:'post',
					data:qs.stringify({'fieldid':data}),
					headers:{
						'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
						'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
					},
				}).then(resp => {
					this.orderTable = resp.data;
					//console.log(resp.data);
				}).catch(error =>{
					console.log('err');
				});

				//是否加入订单的支付状态
				//
				//
			}else{
				this.orderFormVisible=true;
				axios({
					url:'http://localhost:8080/GMS/field/getOneField.do',
					method:'post',
					data:qs.stringify({'fieldid':data}),
					headers:{
						'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
						'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
					},
				}).then(resp => {
					this.fieldname = resp.data.fieldname;
					//console.log(resp.data);
				}).catch(error =>{
					console.log('err');
				});
			}
		}else{
			this.$message.error('错了哦，接受不了后台数据啦！请重新刷新');
		}
   },
        cancelForm(formName){
      //点击取消恢复之前的值
      this.$refs[formName].resetFields();
      this.orderFormVisible=false;
      },
      cancelHForm(formName){
      //点击取消恢复之前的值
      this.$refs[formName].resetFields();
      this.orderHFormVisible=false;
      },
      submitHForm(formName){

		var tempdata ={
			fieldid:'',
			//用户id获取
			userid:'',
			phone:'',
			rentstart:'',
			rentend:'',
			returnid:0,
			allTime:'',
			money:'',
			ouserid:'',
			oorderid:''
		};
    var tempp1='';
    var tempp2='';

		tempdata.oorderid=this.orderTable[0].orderid;
		tempdata.ouserid=this.orderTable[0].userid;
		if (this.pID==2) {
          tempp1=parseInt(this.form.userid);
          //console.log(tempp1);
          tempp2=parseInt(tempdata.ouserid);
          //console.log(tempp2);
        //console.log(tempp2==tempp1);
        //console.log(tempp1)
					if (tempp1!=tempp2&&tempp1!=''&&tempp2!='') {
	      	if(this.form.startDate<=this.form.endDate){

	          //找出时间特殊分隔符号  ":" 截取 并把小时和分钟强转成int类型进行时间戳的计算  
	            var index1 = this.form.startTime.lastIndexOf('\:');
	            var index2 = this.form.endTime.lastIndexOf('\:');
	            //获取分钟位置
	            var tempstart = this.form.startTime.substring(index1+1,this.form.startTime.length);
	            var tempend = this.form.endTime.substring(index2+1,this.form.endTime.length);
	            //this.form.startT=this.form.startDate+' '+this.form.startTime;
	            //console.log(this.form.startTime);
	            //强转
	            var startSeconds = parseInt(this.form.startTime,10)*60*60*1000+parseInt(tempstart,10)*60*1000;
	            var endSeconds = parseInt(this.form.endTime,10)*60*60*1000+parseInt(tempend,10)*60*1000;
	            //console.log(startSeconds);
	            this.orderHFormVisible=false;
	            this.form.startT = this.form.startDate+startSeconds;
	            this.form.endT=this.form.endDate+endSeconds;

				//moment(parseInt(respp[i].rentstart)).format('YYYY-MM-DD hh:mm:ss')
				tempdata.fieldid = this.form.fieldid;
				tempdata.userid = this.form.userid;
				tempdata.phone = this.form.phone;
				tempdata.rentstart = moment(this.form.startT).format('YYYY-MM-DD hh:mm:ss');
				tempdata.rentend = moment(this.form.endT).format('YYYY-MM-DD hh:mm:ss');
				tempdata.allTime = (this.form.endT-this.form.startT)/60/1000;
				tempdata.money = this.form.unitprice*tempdata.allTime/30;

						//console.log(tempdata);
						//
				axios({
					url:'http://localhost:8080/GMS/FieldOrder/orderFieldForEvent.do',
					method:'post',
					headers:{
					'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
					'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
					},
					data:qs.stringify(tempdata)
				}).then(resp => {
					//console.log(resp.data);
					var tempC = resp.data;
					if (tempC==='Success to OrderH') {

						this.$message({
							type: 'success',
							message: '您成功预约了场地'
						});
						//this.reload();
						window.location.href="/GMS/equip/qcgl(BG).html"
					}
				}).catch(err => {
					console.log('后台出错')
				})

	          }else{
	            this.$notify.error({
	            title: '时间选择错误',
	            message: '您不能让开始租赁的时间早于结束租赁时间，这不合理!'
	              });
	          }
		}else{
	            this.$notify.error({
	            title: '错误!',
	            message: '用户无法重复进行赛事申请或您的userid为空!'
	              });	
		}
		}else if (this.pID==1) {
            this.$notify.error({
	            title: '错误：',
	            message: '后台显示您的权限不足，无法进行对重大赛事选项进行操作!!'
	              });
		}
      },
      submitForm(formName) {
     		 		this.$refs[formName].validate((valid) => {
					if (valid) {
		     		 	if(this.form.startDate<=this.form.endDate){

		     		 	//找出时间特殊分隔符号	":" 截取 并把小时和分钟强转成int类型进行时间戳的计算	
		        		var index1 = this.form.startTime.lastIndexOf('\:');
		        		var index2 = this.form.endTime.lastIndexOf('\:');
		        		//获取分钟位置
		        		var tempstart = this.form.startTime.substring(index1+1,this.form.startTime.length);
		        		var tempend = this.form.endTime.substring(index2+1,this.form.endTime.length);
		        		//this.form.startT=this.form.startDate+' '+this.form.startTime;
		        		//console.log(this.form.startTime);
		        		//强转
		        		var startSeconds = parseInt(this.form.startTime,10)*60*60*1000+parseInt(tempstart,10)*60*1000;
		        		var endSeconds = parseInt(this.form.endTime,10)*60*60*1000+parseInt(tempend,10)*60*1000;
		        		//console.log(startSeconds);
						this.orderFormVisible=false;
						this.form.startT = this.form.startDate+startSeconds;
		        		this.form.endT=this.form.endDate+endSeconds;
		        		//console.log(this.form);
		        		//console.log((this.form.endT-this.form.startT)/60/1000+'分钟');

		        		var tempdata ={
							fieldid:'',
							//用户id获取
							userid:'',
							phone:'',
							rentstart:'',
							rentend:'',
							returnid:0,
							allTime:'',
							money:''
						};

						//moment(parseInt(respp[i].rentstart)).format('YYYY-MM-DD hh:mm:ss')
						tempdata.fieldid = this.form.fieldid;
						tempdata.userid = this.form.userid;
						tempdata.phone = this.form.phone;
						tempdata.rentstart = moment(this.form.startT).format('YYYY-MM-DD hh:mm:ss');
						tempdata.rentend = moment(this.form.endT).format('YYYY-MM-DD hh:mm:ss');
						tempdata.allTime = (this.form.endT-this.form.startT)/60/1000;
						tempdata.money = this.form.unitprice*tempdata.allTime/30;

						axios({
							url:'http://localhost:8080/GMS/FieldOrder/orderField.do',
							method:'post',
							headers:{
							'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
							'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
							},
							data:qs.stringify(tempdata)
						}).then(resp => {
							//console.log(resp.data);
							var tempC = resp.data;
							if (tempC==='Success to Order') {
								this.$message({
									type: 'success',
									message: '您成功预约了场地'
								});
								//this.reload();
								//jump
								if (this.pID==1) {
									window.location.href="/GMS/equip/qcgl.html"
								}else{
									window.location.href="/GMS/equip/qcgl(BG).html"

								}
							}

						}).catch(err => {
							console.log('后台出错')
						})




		        		//console.log(moment(this.form.startT).format('YYYY-MM-DD hh:mm:ss'));
		        		//console.log(moment(this.form.endT).format('YYYY-MM-DD hh:mm:ss'));
		     		 	
		     		 	}else{
		     		 		this.$notify.error({
								title: '时间选择错误',
								message: '您不能让开始租赁的时间早于结束租赁时间，这不合理!'
		        			});
		     		 	}	
					} else {
						this.$notify.error({
								title: '选项未填写完毕',
								message: '您仍未填写完毕!!'
		        			});
						return false;
					}
					});
      },
		getCookieUidAndName(){
			let UsrMsg = this.$getCookie('UName');
			var uID = '';
			var uName = '';
			var pID = '';
			if (UsrMsg!='') {
				uID = UsrMsg[0];
				uName = UsrMsg[1];
				pID = UsrMsg[2];
			}
			this.form.userid = parseInt(uID);
			this.name = uName;
			this.pID = parseInt(pID);
		}
 },

  created(){
  	this.getCookieUidAndName();
  	this.fieldEvent();
    this.form.startDate = new Date().getTime()-(new Date().getHours()*60*60*1000+new Date().getMinutes()*60*1000+new Date().getSeconds()*1000+new Date().getMilliseconds());
  this.suretime = new Date().getTime()-(new Date().getHours()*60*60*1000+new Date().getMinutes()*60*1000+new Date().getSeconds()*1000+new Date().getMilliseconds());
  }
  };
