<template>
  <div class="DeleteNotice">
    <el-input placeholder="请输入要查询的信息"  v-model="search" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>

    <el-table
    :data="tables"
    border
    style="width: 70%;margin-left: 15%"
    stripe>
    <!-- <el-table-column
      label="日期"
      width="180">
      <template slot-scope="scope">
        <i class="el-icon-time"></i>
        <span style="margin-left: 10px" v-html="format(scope.row.date)"></span>
      </template>
    </el-table-column> -->
    <el-table-column
      fixed
      prop="noticefid"
      label="公告ID"
      width="150%">
    </el-table-column>
    <el-table-column
      prop="fieldid"
      label="场地ID"
      width="200%">
    </el-table-column>
        <el-table-column
      prop="noticemessage"
      label="相关信息"
      width="400%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.noticemessage)"></span>
      </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      style="text-align: center"
      width="200%">
      <template slot-scope="scope">
        <el-button @click="handleClick(scope.row)" type="primary" style="margin-left: 15%" size="mini">查看</el-button>
        <el-button
          @click.native.prevent="deleteRow(scope/*scope.$index*/, tableData,tables)"
          type="danger"
          size="mini">
          删除
        </el-button>
      </template>
    </el-table-column>
  </el-table>

<div style="margin-top:120px;color:#F00;font-size:30px;margin-bottom:60px">回收过时公告</div>

<el-input placeholder="请输入要查询的信息"  v-model="searcho" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>
    <el-table
    :data="tableso"
    border
    style="width: 90%;margin-left: 5%"
    stripe>
    <el-table-column
      fixed
      prop="noticefid"
      label="公告ID"
      width="150%">
    </el-table-column>
    <el-table-column
      prop="fieldid"
      label="场地ID"
      width="200%">
    </el-table-column>
        <el-table-column
      prop="noticemessage"
      label="相关信息"
      width="400%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.noticemessage)"></span>
      </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      style="text-align: center"
      width="200%">
      <template slot-scope="scope">
        <el-button @click="handleClick(scope.row)" type="primary" style="margin-left: 15%" size="mini">查看</el-button>
        <el-button
          @click.native.prevent="recycleRow(scope/*scope.$index*/)"
          type="danger"
          size="mini">
          回收
        </el-button>
      </template>
    </el-table-column>
  </el-table>

  <el-dialog :visible.sync="dialogTableVisible" lock-scroll>
    <span>
      <h4>公告ID</h4>
       <el-card shadow="hover" style="margin-left: 15%;margin-top: 10px; width: 70%">
          {{tableNotice.noticefid}}
      </el-card>
    </span>
     <span>
     	<h4>公告名称</h4>
	     <el-card shadow="hover" style="margin-left: 15%;margin-top: 10px; width: 70%">
	        {{tableNotice.noticename}}
	    </el-card>
	</span>
	<span>
	<h4>公告内容</h4>
		<el-card shadow="hover" style="margin-left: 15%;margin-top: 10px; width: 70%">
        	{{tableNotice.noticemessage}}
    	</el-card>
    </span>
    <span>
	<h4>场地ID</h4>
		<el-card shadow="hover" style="margin-left: 15%;margin-top: 10px; width: 70%">
        	{{tableNotice.fieldid}}
    	</el-card>
    </span>
    <span>
	<h4>场地名称</h4>
		<el-card shadow="hover" style="margin-left: 15%;margin-top: 10px; width: 70%">
        	{{tableNotice.fieldname}}
    	</el-card>
    </span>
  </el-dialog>
  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
export default {
	//注入
	inject:['reload'],
  data () {
    return {
      dialogTableVisible:false,
      search: '',
      searcho:'',
      tableDataL:6,
      tableData: [
        {
        	noticefid:'',
			noticename:'',
            fieldid:'',
            noticemessage:'',
            fieldname:'',
                                // 发送请求 赋值时候进行运算
          }
      ],
      tableDatao: [
        {
        	noticefid:'',
			noticename:'',
            fieldid:'',
            noticemessage:'',
            fieldname:'',
                                // 发送请求 赋值时候进行运算
          }
      ],
      tableNotice:{
      		noticefid:'',
			noticename:'',
            fieldid:'',
            noticemessage:'',
            fieldname:'',
      }
    }
  },

  computed: {
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }else{
        return this.tableData;
      }
    },
    tableso() {
      const searcho = this.searcho
      if (searcho) {
        return this.tableDatao.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(searcho) > -1
          })
        })
      }else{
        return this.tableDatao;
      }
    }
  },

  methods: {
    handleClick (row) {
      //console.log(row);
      this.tableNotice = row;
      this.dialogTableVisible=true;
    },
     deleteRow(scope, rows,searchTable) {
       var delfieldid = scope.row.fieldid;
     	var control = false;
     	//this.$options.methods.open();
     	//
     	this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {

        	axios({
     		url:'http://localhost:8080/GMS/FieldNotice/delNotice.do',
     		method:'post',
     		headers:{
  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',
				'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
  			},
  			data:qs.stringify({'noticefid':scope.row.noticefid}),
     	}).then(resp => {
     			var tempBool = resp.data;
     		if (tempBool = "Success to DelNotice") {
     			control = true;
     		}
     		if (control) {
     			const loading = this.$loading({
		          lock: true,
		          text: 'Loading',
		          spinner: 'el-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        });
				setTimeout(() => {
					loading.close();
					this.$notify({
				      title: '成功',
				      message: '您删除了一个场地',
				      position:'bottom-right',
				      type: 'success'
					});
					//刷新路由
					this.reload();
					control = false;
				},1000);
     		}else{
     			console.log('err');
     		}
     	}).catch(err => {
     		console.log('err');
     	});
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
       
       
      },
    format (val) {
      if (val.indexOf(this.search) !== -1 && this.search !== '') {
        return val.replace(this.search, '<font color="red">' + this.search + '</font>')
      } else {
        return val;
      }
    },
    formato (val) {
      //val.indexOf(this.search) !== -1 &&
      if (val.indexOf(this.searcho)!==-1&& this.searcho !== '') {
        return val.replace(this.searcho, '<font color="red">' + this.searcho + '</font>')
      } else {
        return val;
      }
    },
    recycleRow(scope){
    	//恢复
      //
      var control = false;
      axios({
        url:'http://localhost:8080/GMS/FieldNotice/recycleNotice.do',
        method:'post',
        headers:{
          'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//第一个转码格式是resp的时候转成utf-8
          'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//第二个转码格式是req负载（payload）带参数的时候转成utf-8到后台
        },
        data:qs.stringify({'noticefid':scope.row.noticefid}),
      }).then(resp => {
          var tempBool = resp.data;
        if (tempBool = "Success to RecycleNotice") {
          control = true;
        }
        if (control) {
          const loading = this.$loading({
              lock: true,
              text: 'Loading',
              spinner: 'el-icon-loading',
              background: 'rgba(0, 0, 0, 0.7)'
            });
        setTimeout(() => {
          loading.close();
          this.$notify({
              title: '成功',
              message: '您恢复了一个场地',
              position:'bottom-right',
              type: 'success'
          });
          //刷新路由
          this.reload();
          control = false;
        },1000);
        }else{
          console.log('err');
        }
      }).catch(err => {
        console.log('err');
      });
    },
    getAllNotice(){
    	axios({
    		url:'http://localhost:8080/GMS/FieldNotice/getDelAll.do',
    		method:'post',
    		headers:{
    			'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',
				'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
    		}
    	}).then(resp => {
    		this.tableData = resp.data;

    	}).catch(err => {
    		console.log('err');
    	});

    	axios({
    		url:'http://localhost:8080/GMS/FieldNotice/getDelAllwDel.do',
    		method:'post',
    		headers:{
    			'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',
				'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',    			
    		}
    	}).then(resp => {
    		this.tableDatao = resp.data;

    	}).catch(err => {
    		console.log('err');
    	});
    }
  },
  created(){
  	this.getAllNotice();
  }

}
</script>

<style scoped>

</style>


