<template>
  <div id="web">
	<el-container>
		<el-header style="height: 180px">
			<headerLayout></headerLayout>
		</el-header>
		<el-main>
			<router-view v-if="isRouterAlive"></router-view>
		</el-main>
		<el-footer style="height:200px">
			<foot></foot>
		</el-footer>
	</el-container>
	<div id="loginform">
		<loginform></loginform>
	</div>
	<div id="registerform">
		<registerform></registerform>
	</div>
  </div>
</template>

<script>
  import headerLayout from './modules/head/HeaderLayout.vue'
  import foot from './modules/foot/Foot.vue'
  import loginform from './modules/head/loginAndRegister/LoginForm.vue'
  import registerform from './modules/head/loginAndRegister/UserRegister.vue'
export default {
  name: 'web',
  provide(){
  	return {
  		reload:this.reload
  	}
  },
  data(){
  	return{
  		isRouterAlive:true
  	}
  },
  methods:{
  	//刷新路由
  	reload(){
  		this.isRouterAlive = false;
  		this.$nextTick(function(){
  			this.isRouterAlive = true;
  		})
  	}
  },
  components:{
    headerLayout,
    foot,
    loginform,
    registerform

  }
}
</script>

<style scoped>
</style>



