<template>
	<!-- #18367C -->
	<div class="navMenu">
		<!-- :route="{path:'/area'}" -->
		<el-menu
			  :default-active="activeIndex"
			  class="el-menu-demo"
			  mode="horizontal"
			  @select="handleSelect"
			  background-color="#18367C"
			  text-color="#fff"
			  active-text-color="#f0e0a0"
			  style="width:100%;height: 90%;font-size: 20px;"
			  router
			  >
	  	<el-menu-item index="/Home">
		  	<i class="el-icon-s-home el-icon--left" style="color: #fff"></i>首页
		</el-menu-item>

		<el-submenu index="/Field">
		    <template slot="title">
		    	<i class="el-icon-place el-icon--left" style="color: #fff"></i>场地
		    </template>

		    <el-submenu index="/FieldShow">

		     	<template slot="title">
		      		场地预览
		  		</template>

		      	<el-menu-item index="/Field/FieldShow/OutSide">
			      	室外
		      	</el-menu-item>

		      	<el-menu-item index="/Field/FieldShow/InSide">
		      		室内
		  		</el-menu-item>

		    </el-submenu>
			<el-submenu index="/FieldOrder">
				<template slot="title">
					场地预览预约
				</template>
				<el-menu-item index="/Field/FieldOrder/Available" :disabled="ifUser">
					可用场地
				</el-menu-item>
				<el-menu-item index="/Field/FieldOrder/Borrowed">
					已被借用
				</el-menu-item>
				<el-menu-item index="/Field/FieldOrder/Maintenance">
				维护中...
				</el-menu-item>
			</el-submenu>
		</el-submenu>
		<el-menu-item index="/MessageCenter" :disabled="ifUser">
		  	<i class="el-icon-s-data el-icon--left" style="color: #fff"></i>消息中心
		</el-menu-item>
		  <el-submenu
		  index="/adminControl" 
		  :disabled="adminRoot"
		  >
		  <template slot="title">
		  	<i class="el-icon-lock el-icon--left"></i>场地管理
		  </template>
				<el-menu-item index="/adminControl/AddField">
					添加场地
				</el-menu-item>
				<el-menu-item index="/adminControl/DeleteField">
					删除场地
				</el-menu-item>
				<el-menu-item index="/adminControl/ChangeField">
				  	修改场地
				</el-menu-item>
				<el-menu-item index="/adminControl/CancelField" disabled>
				  管理员撤销场地
				</el-menu-item>
				<el-submenu index="/ManageCenter">
					<template slot="title">
						管理员处理中心
					</template>
				  	<el-submenu index="/Notice">
				  		<template slot="title">
				  			公告中心
				  		</template>
				  		<el-menu-item index="/Notice/Add">
							添加公告
						</el-menu-item>
						<el-menu-item index="/Notice/Del">
							删除公告
						</el-menu-item>
						<el-menu-item index="/Notice/Change">
						  	修改公告
						</el-menu-item>
				  	</el-submenu>
				  	<el-submenu index="/BlackList" disabled>
				  		<template slot="title">
				  			黑名单
				  		</template>
				  		<el-menu-item index="/BlackList/Add">
							新增黑名单
						</el-menu-item>
						<el-menu-item index="/BlackList/Del">
							删除黑名单
						</el-menu-item>
						<el-menu-item index="/BlackList/Change">
						  	修改黑名单
						</el-menu-item>
				  	</el-submenu>
				  	<el-submenu index="/Regulation">
				  		<template slot="title">
				  			条例
				  		</template>
				  		<el-menu-item index="/Regulation/Add">
							新增条例
						</el-menu-item>
						<el-menu-item index="/Regulation/Del">
							删除条例
						</el-menu-item>
						<el-menu-item index="/Regulation/Change">
						  	修改条例
						</el-menu-item>
				  	</el-submenu>
				</el-submenu>
			</el-submenu>
			<el-menu-item index="/ResolutionCenter" :disabled="adminRoot||application||important">
		  	<i class="el-icon-s-help el-icon--left" style="color: #fff"></i>重大赛事处理中心
		</el-menu-item>
		</el-menu>
	</div>
</template>

<script>
	export default{
		data(){
			return {
				activeIndex:'1',
				adminRoot:true,
				application:true,
				important:true,
				ifUser:true
			}
		},
		methods:{
			handleSelect(key,keyPath){
				//console.log(key,keyPath);
			},
			getIfAdmin(){
				let UsrMsg = this.$getCookie('UName');
				var uPID='';
				if (UsrMsg!='') {
				uPID = UsrMsg[2];
				//userid  uID
				//UsrMsg[0]
				//username  uName
				//UsrMsg[1]
				//root 		uPID
				//uPID = UsrMsg[2];
				//
				if(uPID=='1'){
					console.log('user');
					this.ifUser=false;
					this.application=false;
				}else if(uPID=='2'){
					this.adminRoot=false;
					this.application=false;
					this.important=false;
					this.ifUser=false;
				}else{
					console.log('visitor')
				}
				}
			}
		},
		created(){
			//初始化调用
			this.getIfAdmin()
		}
	}
	
</script>

<style scoped type="less">

</style>


