<template>
	<div class="AddBlackList" style="width: 90%;margin-left: 10%">
		<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
		  	<el-form-item label="用户id" prop="userid">
		    	<el-input v-model="ruleForm.userid" style="width: 25%" @blur="findUsr"  @focus="setNull"></el-input>
		    	<el-input 
	          v-model="username" 
	          disabled 
	          style="width:50%" 
	          placeholder="此处导出名字" 
	          v-loading="loading1"
	          ></el-input>
		 	 </el-form-item>
		  	<el-form-item label="条例id" prop="regulationid">
		    	<el-input type="text" v-model="ruleForm.regulationid" style="width: 25%" @blur="findReg" @focus="setNull1"></el-input>
	        <el-input 
	          v-model="regname" 
	          readonly 
	          style="width:50%" 
	          placeholder="此处导出条例名称"
	  		  disabled           
	          v-loading="loading2"
	          ></el-input>
		  	</el-form-item>
		  	<el-form-item label="违例原因" prop="vioreason">
		    	<el-input type="textarea" v-model="ruleForm.vioreason" style="width: 75%"></el-input>
		  	</el-form-item>
		  	<el-form-item label="处理" prop="solution">
		  		<el-input type="textarea" v-model="ruleForm.solution" style="width: 75%"></el-input>
			</el-form-item>
		  </el-form-item>
		  <el-form-item>
		    <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
		    <el-button @click="resetForm('ruleForm')">重置</el-button>
		  </el-form-item>
		</el-form>
	</div>
</template>
<script>
  export default {
    data() {

      return {
        loading1:false,
        loading2:false,
      	username:'',
        regname:'',
        ruleForm: {
         userid:'',
         regulationid:'',
         vioreason:'',
         solution:''
        },
        rules: {
          userid: [
            { required: true, message: '请输入', trigger: 'blur' }
          ],
          regulationid: [
            { required: true, message: '请输入', trigger: 'blur' }
          ],
          vioreason: [
            { required: true, message: '请填写原因', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
		          const rLoading = this.$showL();
          if (valid) {
  					this.$notify({
  			          title: '成功',
  			          message: '您新增了一个场地',
  			          position:'bottom-right',
  			          type: 'success'
  		        	});
					   return true;
          } else {
            console.log('error submit!!');
            rLoading.close();
            // setTimeout(() => {
            	// loading.close();
            	this.$notify({
		          title: '失败',
		          message: '您的信息有误',
		          position:'bottom-right',
		          type: 'error'
		        });
		        return false;
            // },300);
            
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.username='';
        this.regname='';
        this.loading2=false,
        this.loading1=false;
      },
      findUsr(){
      	this.loading1=true;
        //console.log(this.ruleForm.userid);
        if (this.ruleForm.userid!=='') {
        //请求
          this.username='hahahah';
          this.loading1=false;
        }else{

          this.username='';
        }
      	
      },
      findReg(){
        this.loading2=true;
        if (this.ruleForm.regulationid!=='') {
          //请求
          this.regname='xixiix';
          this.loading2=false;
        }else{
          this.regname=''
        }
      },
      setNull(){
      	this.username=''
      },
      setNull1(){
        this.regname=''
      }
    }
  }
</script>

<style>
	
</style>


