     		 			console.log('放于asset的img和放于static目录下img在loader通过require()方法处理后获取的地址一致'+'TO:'+'/static/img/1.7f31e1a.jpg&&&/static/img/1.7f31e1a.jpg');
     		 			console.log(require('../../../../../assets/2.jpg')+''+require('../../../../../assets/1.jpg'));
     		 			// 总数同步
     		 			this.totalnum=this.sites.length;
     		 		}
     		 	})*/
     		 	axios({
						url:'http://localhost:8080/GMS/field/getInOrOutSide.do',
