     		 	this.tableSite=obj;

     		 }
		},
		created(){
			this.handleSites();


/* 		 	console.log('1室内游泳   '+require('../../../../../../static/img/1.jpg'));
			console.log('2室外游泳   '+require('../../../../../../static/img/2.jpg'));
			console.log('3室内桌球   '+require('../../../../../../static/img/billiards.jpg'));
			console.log('4室内羽毛球 '+require('../../../../../../static/img/iBadminton.jpg'));
			console.log('5室内篮球   '+require('../../../../../../static/img/iBasketball.jpg'));
			console.log('6室内乒乓球 '+require('../../../../../../static/img/iTableTennis.jpg'));
			console.log('7室内排球   '+require('../../../../../../static/img/iVolleyball.jpg'));
			console.log('8室外羽毛球 '+require('../../../../../../static/img/oBadminton.jpg'));
			console.log('9室外篮球   '+require('../../../../../../static/img/oBasketball.jpg'));
			console.log('10室外足球  '+require('../../../../../../static/img/oFootball.jpg'));
			console.log('11室外网球  '+require('../../../../../../static/img/oTennis.jpg'));
			console.log('12室外排球  '+require('../../../../../../static/img/oVolleyball.jpg'));
			*/

		},
		computed:{

		}
	}
</script>

<style scoped>
	.el-row{
		margin-top: 10px;
	}
	.el-col {
		padding-top: 10px;
		border-radius: 4px;
	}
	.bg-purple-dark {
		background: #99a9bf;
	}
	.bg-purple {
		background: #d3dce6;
	}
	.bg-purple-light {
		background: #e5e9f2;
	}
	.grid-content {
		border-radius: 4px;
		min-height: 36px;
	}
</style>


